<template>
  <div id="TipText">
    <van-nav-bar
      :title="$('职位申请')"
      :left-text="$t('返回')"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="Tip">
      <h2>{{ $t('提交成功') }}</h2>
      <br />
      <div class="Text">{{ $t('信息审核通过后将于面试前一天发出面试通知！') }}</div>
      <br />
      <div class="Text">{{ $t('报名结果/面试进度请至【我的申請】查询！') }}</div>
      <br />
      <van-button type="primary" @click="HomePageClick">{{ $t('我 已 知 晓') }}</van-button>
    </div>

  </div>
</template>

<script>
export default {
  name: 'TipText',
  data () {
    return {
      TipText: ''
    }
  },
  mounted () {
    this.TipText = this.$route.params.Text
    console.log(this.$route.params.Text)
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    HomePageClick () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped lang="scss">
  #TipText{
    height: 100%;
    background-color: white;
    text-align: center;
    .Tip{
       margin-top: 40%;
      .Text{
        width: auto;
        padding: 0 10px;
        margin-bottom: 15px;
      }
    }
  }
</style>



