<template>
  <div id="JobFolder">
    <!-- 顶部吸附 -->
    <van-sticky :offset-top="0">
      <!--下拉选择-->
      <van-dropdown-menu>
        <van-dropdown-item :title="$t('部门')" ref="departmentref">
        <!--<van-row type="flex" justify="space-between" class="DropdownItemSlots">
            <van-tag v-for="(item, index) in departmentAll" :key='index' plain type="primary" size="medium"
              :class="{default:!item.selectStatus,Selected:item.selectStatus}" @click="DepartmentClick(index,'departmentAll')">{{item.text}}</van-tag>
          </van-row> -->
          <div v-for="(item, index) in departmentAll" :key="index" class="JobTypeSlots" @click="DepartmentClick(index,'departmentAll')">
            <span :class="{defaultText:!item.selectStatus,SelectedText:item.selectStatus}">{{item.text}}</span>
            <van-icon v-if="item.selectStatus" name="success" color="#ee0a24" />
          </div>
          <div style="padding: 5px 16px;">
            <van-button type="danger" block round @click="ConfirmQueryClick('departmentAll')">
              {{ $t('确认') }}
            </van-button>
          </div>
        </van-dropdown-item>

        <van-dropdown-item :title="$t('岗位类型')" ref="JobTyperef">
          <div v-for="(item, index) in JobTypeAll" :key="index" class="JobTypeSlots" @click="DepartmentClick(index,'JobTypeAll')">
            <span :class="{defaultText:!item.selectStatus,SelectedText:item.selectStatus}">{{item.text}}</span>
            <van-icon v-if="item.selectStatus" name="success" color="#ee0a24" />
          </div>
          <div style="padding: 5px 16px;">
            <van-button type="danger" block round @click="ConfirmQueryClick('JobTypeAll')">
              {{ $t('确认') }}
            </van-button>
          </div>
        </van-dropdown-item>

        <van-dropdown-item :title="$t('厂区')" ref="Locationref">
          <div v-for="(item, index) in LocationAll" :key="index" class="JobTypeSlots" @click="DepartmentClick(index,'LocationAll')">
            <span :class="{defaultText:!item.selectStatus,SelectedText:item.selectStatus}">{{item.text}}</span>
            <van-icon v-if="item.selectStatus" name="success" color="#ee0a24" />
          </div>
          <div style="padding: 5px 16px;">
            <van-button type="danger" block round @click="ConfirmQueryClick('LocationAll')">
              {{ $t('确认') }}
            </van-button>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
      <!--搜索框-->
      <van-search v-model="SearchValue" :placeholder="$t('共')+ PositionDataListcount +$t('条数据，请输入搜索关键词')" @search="OnSearch"/>
      <!-- 公告 -->
      <!-- <h4 style="text-align: center;color: #EE0A24;margin: 0px;">功能模块开发中{{ this.$store.getters.getInfo.empno }}</h4> -->
      <van-notice-bar left-icon="volume-o" :scrollable="false">
        <van-swipe vertical class="notice-swipe"
          :autoplay="3000" :show-indicators="false">
          <van-swipe-item v-for="item in announcementArr" :key="item.SIGN_NO" @click="ViewJobListings(item)">{{ item.THEME }}</van-swipe-item>
        </van-swipe>
      </van-notice-bar>
     </van-sticky>
     <van-overlay :show="overlayShow" @click="overlayShow = false" :lock-scroll="false">
       <div class="wrapper" @click.stop>
         <div class="block" v-html="RichText"></div>
         <div class="btn">
           <van-button icon="cross" type="primary" round @click="overlayShow = false"></van-button>
         </div>
       </div>
     </van-overlay>
    <!--下拉刷新模块-->
    <van-pull-refresh class="JobContainer" v-model="isLoading" @refresh="OnRefresh" ref="JobFolderList">
      <!--列表-->
      <div class="PositionList" v-for="(item, index) in PositionDataList" :key="index" @click="RouteJumpClick(item.id)">
        <div class="Title">
          <div><b>{{$t(item.Title)}}</b></div>
          <div class="Column">{{$t(item.Column)}}</div>
        </div>
        <div class="Time">{{item.Time}}</div>
      </div>
    </van-pull-refresh>

    <van-tabbar v-model="active" route>
      <van-tabbar-item icon="friends-o" to="/Recommend">{{ $t('普工推荐') }}</van-tabbar-item>
      <van-tabbar-item icon="debit-pay" to="/">{{ $t('干部推荐') }}</van-tabbar-item>
      <van-tabbar-item icon="manager-o" to="/Mine">{{ $t('我的') }}</van-tabbar-item>
    </van-tabbar>

    <van-popup v-model="popupShow" position="bottom" :style="{ height: '100%' }">
      <index @CadreRecommendation="CadreRecommendation"></index>
    </van-popup>
  </div>
</template>

<script>
import {
  GetIRListPhone, // 获得需求列表
  GetIRBusinessoffice, // 獲取所有事業處詳細信息
  getWebNotice, // 获取公告列表
  GetContentById // 获取公告详情
} from '@/model/api'
import index from './Index.vue'
export default {
  name: 'JobFolder',
  data () {
    return {
      active: 1,
      popupShow: false, // a
      overlayShow: false, // 是否展示公告
      announcementArr: [], // 全部的公告数据
      RichText: '', // 展示的公告
      // 部门
      departmentAll: [],
      department: [],
      // 岗位类型
      JobTypeAll: [{ text: '', value: '', selectStatus: false }],
      JobType: [],
      // 地点
      LocationAll: [{ text: '', value: '', selectStatus: false }],
      Location: [],
      SearchValue: '', // 搜索框值
      isLoading: false,
      PositionDataListcount: '', // 总共有多少笔数据
      PositionDataList: [],
      scrollTop: 0, // 记录跳转到岗位详情前的滚动条数据
      beForeNama: '' // 上一个页面的 name
    }
  },
  computed: {

  },
  // 页面钩子执行前触发
  beforeRouteEnter (to, from, next) {
    console.log(from.name)
    next(vm => {
      vm.beForeNama = from.name
    })
  },
  // 当组件激活时调用的生命周期钩子函数
  activated () {
    this.$nextTick(() => {
      if (this.beForeNama === 'JobDetails') {
        this.$refs.JobFolderList.$el.scrollTop = this.scrollTop
      }
      if (!this.beForeNama) {
        this.popupShow = true
      }
      this.$route.meta.keepAlive = false
    })
  },
  created () {
    // 获取公告
    this.getAnnouncement()
  },
  mounted () {
    // console.log(this.$store.getters.getInfo)
    // console.log(this.$route.query.empno)
    // console.log(this.$route.query.name)
    // let obj = {
    //   Info: {
    //     empno: this.$route.query.empno ? this.$route.query.empno : 'F1338096',
    //     name: this.$route.query.name
    //   }
    // }
    // if (this.$store.getters.getInfo.empno === '') {
    //   this.$store.commit('update', obj)
    // }
    // 獲取所有事業處詳細信息
    GetIRBusinessoffice().then(res => {
      if (res.status === 0) {
        this.departmentAll = []
        res.data.colBuNames.map(item => {
          this.departmentAll.push({
            text: item,
            value: item,
            selectStatus: false
          })
        })
        this.JobTypeAll = []
        res.data.positionNames.map(item => {
          this.JobTypeAll.push({
            text: item,
            value: item,
            selectStatus: false
          })
        })
        this.LocationAll = []
        res.data.workLocations.map(item => {
          this.LocationAll.push({
            text: item,
            value: item,
            selectStatus: false
          })
        })
      }
    })
    // 默认查询
    this.OnSearch()
  },
  methods: {
    // 干部推荐
    CadreRecommendation (str) {
      this.popupShow = false
    },
    // 获取公告
    getAnnouncement () {
      getWebNotice({
        emp: this.$store.getters.getInfo.empno,
        realeaseType: '通知公告',
        showPage: '[香信端]社招推荐政策',
        site: 'GW,GZ,HT',
        page: 1,
        size: 10
      }).then(res => {
        if (res.Status === 0 && res.Data.list.length > 0) {
          this.announcementArr = res.Data.list
          this.announcementArr.map(item => {
            GetContentById({
              rowId: item.ROW_ID
            }).then(res => {
              if (res.Status === 0) {
                this.$set(item, 'richText', res.Data)
                if (this.beForeNama === null) {
                  this.RichText = res.Data
                  this.overlayShow = true
                }
              } else { this.$toast(res.Message) }
              this.show = false
            })
          })
        } else { this.$toast(res.Message) }
      })
    },
    // 顶部下拉框
    DepartmentClick (index, Arr) {
      switch (Arr) {
        case 'departmentAll':
          this.departmentAll[index].selectStatus = !this.departmentAll[index].selectStatus
          break
        case 'JobTypeAll':
          this.JobTypeAll[index].selectStatus = !this.JobTypeAll[index].selectStatus
          break
        case 'LocationAll':
          this.LocationAll[index].selectStatus = !this.LocationAll[index].selectStatus
          break
        default:
      }
    },
    // 确认搜索条件
    ConfirmQueryClick (key) {
      // this.$refs.departmentref.toggle(fasle)
      // false:关闭弹窗， true:打开弹窗， 不传为取反
      switch (key) {
        case 'departmentAll': // 事业处
          this.$refs.departmentref.toggle(false)
          break
        case 'JobTypeAll': // 岗位类型
          this.$refs.JobTyperef.toggle(false)
          break
        case 'LocationAll': // 工作地点
          this.$refs.Locationref.toggle(false)
          break
        default:
      }
      this.department = []
      this.departmentAll.map(item => {
        if (item.selectStatus) {
          this.department.push(item.text)
        }
      })
      this.JobType = []
      this.JobTypeAll.map(item => {
        if (item.selectStatus) {
          this.JobType.push(item.text)
        }
      })
      this.Location = []
      this.LocationAll.map(item => {
        if (item.selectStatus) {
          this.Location.push(item.text)
        }
      })
      this.OnSearch()
    },
    // 搜索框
    OnSearch () {
      this.isLoading = true
      // 获得需求列表
      GetIRListPhone({
        colBuNames: this.department, // 事业处
        positionIs: this.JobType, // 岗位类型
        workLocations: this.Location, // 工作地点
        positionName: this.SearchValue, // 岗位名称 搜索框
        page: 1, // 页
        rows: 9999 // 行
      }).then(res => {
        if (res.status === 0) {
          this.PositionDataList = []
          this.PositionDataListcount = res.data.count
          res.data.records.map(item => {
            this.PositionDataList.push({
              Title: item.positionName,
              Column: item.positionI,
              Time: item.publishDate.slice(0, 10),
              id: item.demandNo
            })
          })
        }
        this.isLoading = false
        if (this.beForeNama !== null || !this.popupShow) {
          this.$toast(this.$t('数据获取成功'))
        }
      })
    },
    // 下拉刷新事件
    OnRefresh () {
      // setTimeout(() => {
      //   this.isLoading = false
      //   this.$toast('刷新成功')
      // }, 1000)
      this.OnSearch()
      // if (this.isLoading) {
      //   this.$toast('刷新成功')
      // }
    },
    // 点击查看详情
    RouteJumpClick (id) {
      this.$route.meta.keepAlive = true
      this.scrollTop = this.$refs.JobFolderList.$el.scrollTop
      this.$router.push({path: '/JobDetails', query: { Id: id }})
    },
    // 公告的按钮点击事件
    ViewJobListings (item) {
      console.log(item)
      this.RichText = item.richText
      this.overlayShow = true
    }
  },
  components: {
    index
  }
}
</script>

<style scoped lang="scss">
  #JobFolder{
    height: 100%;
    .TopFixed{
      width: 100%;
      height: 102px;
      background-color: cornflowerblue;
      position: fixed;
      z-index: 20;
      top: 0;
      left: 0;
    }
    // 顶部导航模块
    .DropdownItemSlots {
      padding: 0 10px;
      span {
        padding: 5px;
        margin: 3px 0;
      }
      .default {
        color: #9bb7d3;
      }
      .Selected {
        color: #ee0a24;
      }
    }
    .JobTypeSlots {
      padding: 10px 16px;
      display: flex;
      justify-content: space-between;
      .defaultText {
        color: #666666;
      }
      .SelectedText {
        color: #ee0a24;
      }
    }
    // 公告
    .wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .block {
        height: 30rem;
        width: 95%;
        overflow: scroll;
        border-radius: 10px;
        background-color: #f6ffff;
      }
      .btn {
        padding-top: 0.5rem;
      }
    }
    // 刷新列表模块
    .JobContainer{
      height: calc(100% - 123px - 50px);
      padding: 10px;
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      background: #f4f8ff;
      .PositionList{
        display: flex;
        background-color: #FFFFFF;
        border-radius: 5px;
        padding:10px;
        margin-bottom: 10px;
        box-shadow:0px 15px 10px -15px #000;
        .Title{
          line-height: 20px;
          text-align: left;
          color: #333;
          .Column{
            font-size: 12px;
            color: #666;
          }
        }
        .Time{
          line-height: 40px;
          margin-left: auto;
        }
      }
    }
  }
</style>
<style lang="scss">
  #JobFolder{
    .van-notice-bar {
      height: 25px;
      padding: 0 15px;
    }
    .van-overlay {
      background-color: #00000025;
    }
  }
</style>



