<template>
  <div id="RecommendDetails">
    <van-nav-bar
      :title="$t('普工推薦政策')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />

    <div class="Content">
      <div class="Title" style="padding-top: 5%;">{{ $t('推薦政策：') }}</div>
      <div class="Table">
        <table  cellpadding="0" cellspacing="0">
          <tr>
            <td rowspan="2"></td>
            <td colspan="5">{{ $t('被推荐人入职时间') }}</td>
          </tr>
          <tr>
            <td>{{ $t('满1个月') }}</td>
            <td>{{ $t('满2个月') }}</td>
            <td>{{ $t('满3个月') }}</td>
            <td>{{ $t('满6个月') }}</td>
            <td>{{ $t('合计') }}</td>
          </tr>
          <tr>
            <td>{{ $t('推荐人') }}</td>
            <td>400</td>
            <td>300</td>
            <td>200</td>
            <td>600</td>
            <td>1500</td>
          </tr>
          <tr>
            <td>{{ $t('被推荐人') }}</td>
            <td>120</td>
            <td>120</td>
            <td>120</td>
            <td> - </td>
            <td>360</td>
          </tr>
        </table>
      </div>
      <div class="Remark"><span>{{ $t('备注：次集团新人入职奖金') }}</span></div>

      <div class="Title" style="padding-top: 10%;">{{ $t('【推荐人要求】') }}</div>
      <div>{{ $t('1.富士康各事业群在职员工（正式+派遣）。') }}</div>
      <div class="Title">{{ $t('【被推荐人要求】') }}</div>
      <div>{{ $t('1.符合国家法定就业年龄，男女不限；') }}</div>
      <div>{{ $t('2.初中(含)以上学历；') }}</div>
      <div>{{ $t('3.身心健康且无影响团体健康之疾病；') }}</div>
      <div>{{ $t('4.二次入职条件：正常离职满1个月/自离满3个月。') }}</div>
    </div>

    <!--分享-->
    <van-share-sheet
      v-model="ShareShow"
      :title="$t('分享')"
      :options="ShareArr"
      @select="onSelect()"
      @close="onClose()"
    />

    <van-goods-action style="border-top: #F1F1F1 solid 1px">
      <van-goods-action-icon icon="share-o" :text="$t('分享')" @click=" onClickButton(1) " />
      <van-goods-action-button
        type="primary"
        :text="$t('立即申请')"
        @click="onClickButton(2)"
      />
    </van-goods-action>
  </div>
</template>

<script>
export default {
  name: 'RecommendDetails',
  data () {
    return {
      Data: 'aaaaaaa',
      ShareShow: false, // 分享
      ShareArr: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' }
        ]
      ]
    }
  },
  methods: {
    // 底部按钮
    onClickButton (num) {
      if (num === 1) { // 分享岗位
        this.ShareShow = true
      } else if (num === 2) { // 岗位推荐
        this.$router.push({path: '/GeneralWorkerRecommendation', query: {}})
      }
    },
    // 关闭分享面板
    onClose () {
      this.ShareShow = false
    }
  }
}
</script>

<style scoped lang="scss">
#RecommendDetails {
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  .Content {
    height: calc(100% - 46px - 51px);
    padding: 0 5%;
    // background: #f4f8ff;
    .Title {
      color: #333333;
      font-size: 18px;
      padding: 10px 0;
    }
    .Table {
      width: 100%;
      table {
        font-size: 12px;
        width: 100%;
        border-top: #333333 solid 1px;
        border-left: #333333 solid 1px;
        background: #ffffc8;
        td {
          text-align: center;
          padding: 3px 5px;
          border-right: #333333 solid 1px;
          border-bottom: #333333 solid 1px;
        }
      }
    }
    .Remark {
      color: #666666;
      font-size: 12px;
      margin: 5px 0;
      span {
        padding: 2px 5px;
        border: #333333 solid 1px;
      }
    }
  }
}
</style>



