<template>
  <div id="GeneralWorkerRecommendation">
    <van-nav-bar
      :title="$t('普工推荐基本信息')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />
    <div class="Content">
      <!--推荐人-->
      <div class="Referrer">
        <van-form @submit="OnSubmit">
          <h4>{{ $t('推荐人基本信息') }}</h4>
          <van-field v-model="Data.reeNo" :label="$t('工号')" :placeholder="$t('请输入工号')" :rules="[{ required: true }]" input-align="right" @change="ReeNoChange"/>
          <van-field v-model="Data.reeName" :label="$t('姓名')" :placeholder="$t('请输入姓名')" :rules="[{ required: true }]" input-align="right"/>
          <van-field :value="Data.reePlant" :label="$t('厂区')" :placeholder="$t('请选择厂区')" @click="SelectorClick('Data.reePlant')"
              readonly clickable name="picker" :rules="[{ required: true }]"/>
          <van-field v-model="Data.reeMobile" :label="$t('手机号码')" :placeholder="$t('请输人手机号码')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Data.reeDept" :label="$t('所在单位')" :placeholder="$t('请输入所在单位')" :rules="[{ required: true }]" input-align="right"/>
          <van-field :value="$t(Data.relationship)" :label="$t('与被推荐人关系')" :placeholder="$t('请选择与被推荐人关系')" @click="SelectorClick('Data.relationship')"
            readonly clickable name="picker" :rules="[{ required: true }]"/>
          <van-field v-model="Data.reeIdnumber" :label="$t('身份证号')" :placeholder="$t('请输入身份证号')" :rules="[{ required: false }]" input-align="right"/>
          <!--
          <van-field v-model="Data.bank" :label="$t('开卡行')" :placeholder="$t('请输入开卡行')" :rules="[{ required: false }]" input-align="right"/>
          <van-field v-model="Data.bankNo" :label="$t('银行卡号')" :placeholder="$t('请输入银行卡号')" :rules="[{ required: false }]" input-align="right"/>
          -->
          <h4>{{ $t('被推荐人基本信息') }}</h4>
          <van-field v-model="Data.ralName" :label="$t('姓名')" :placeholder="$t('请输入姓名')" :rules="[{ required: true }]" input-align="right"/>
          <!--tuyen add-->
          <van-field v-model="Data.ralSex" :label="$t('性別')" :placeholder="$t('被推薦人性別')" :rules="[{ required: true }]" input-align="right"/>
           <!--tuyen add-->
          <van-field readonly clickable name="calendar" :value="Data.ralAge" :label="$t('出生日期')" :placeholder="$t('被推薦人出生日期')"
            @click="showCalendar1 = true" :rules="[{ required: true }]"/>
          <van-calendar v-model="showCalendar1" :min-date="minDate1" :max-date="maxDate1" @confirm="onConfirm1" />
          <van-field v-model="Data.ralMobile" :label="$t('联系方式')" :placeholder="$t('请输入联系方式')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Data.ralIdnumber" :label="$t('身份证号')" :placeholder="$t('请输入身份证号')" :rules="[{ required: true }]" input-align="right"/>
          <!--tuyen add-->
          <van-field v-model="Data.ralUnit" :label="$t('被推薦人单元')" :placeholder="$t('请输入被推薦人单元')" :rules="[{ required: true }]" input-align="right"/>
          <van-field readonly clickable name="calendar" :value="Data.preDate" :label="$t('预面试日期')" :placeholder="$t('点击选择预面试日期')"
            @click="showCalendar = true" :rules="[{ required: true }]"/>
          <van-calendar v-model="showCalendar" @confirm="onConfirm"  :max-date="maxDate"/>
          <van-popup v-model="showPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="columns"
              @confirm="OnConfirm"
              @cancel="showPicker = false"
            />
          </van-popup>

          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">{{ $t('提交') }}</van-button>
          </div>
        </van-form>
      </div>
    </div>

  </div>
</template>

<script>
import {
  AddIRGeneralWorker, // 提交推荐信息
  GetIRMyMessage // 获取我填单的历史信息
} from '@/model/api'
export default {
  name: 'GeneralWorkerRecommendation',
  data () {
    return {
      showPicker: false, // 选择器是否打开
      showCalendar: false, // 日期选择器是否打开
      showCalendar1: false, // show showCalendar for ralAge
      minDate1: new Date(1980, 0, 1),
      maxDate1: new Date(2100, 0, 31),
      maxDate: new Date(2100, 0, 31),
      columns: [], // 选择器数组
      TemporaryKey: '', // 选择器临时中转的key值
      Relationship: [this.$t('亲戚'), this.$t('朋友'), this.$t('前同事'), this.$t('同学'), this.$t('其它')], // 与被推荐人关系
      // RelationshipVN:['Người thân', 'Bạn bè', 'Đồng nghiệp cũ', 'Bạn học', 'Khác'],
      reePlantArr: ['光州工業區', '桂武工業區', '黃田工業區'], // 推荐人厂区
      Data: {
        reeName: '', // 推薦人姓名
        reeNo: '', // 推薦人工號
        reePlant: '光州工業區', // 推荐人厂区
        reeMobile: '', // 推薦人聯繫方式
        reeDept: '', // 推薦人部門
        relationship: '', // 與被推薦人的關係
        reeIdnumber: '', // 推薦人身份證號碼
        bank: '', // 開卡銀行
        bankNo: '', // 銀行卡號
        ralName: '', // 被推薦人姓名
        ralSex: '', // 被推薦人性別
        ralIdnumber: '', // 被推薦人身份證號碼
        ralAge: '', // 被推薦人年齡
        ralMobile: '', // 被推薦人手機號碼
        ralUnit: '', //  被推薦人单元 tuyen add
        preDate: '', // 預面試日期 2021-12-17
        beginTime: '', // 起始時間 2021-12-17 没啥用，暂时先预定义着
        endTime: '', // 結束時間 2021-12-18 没啥用，暂时先预定义着
        status: 0 // 狀態 没啥用，暂时先预定义着
      }
    }
  },
  mounted () {
    this.Data.reeNo = this.$store.getters.getInfo.empno
    this.ReeNoChange()
  },
  methods: {
    MapLangRelationship (Relationship) {
      if ((Relationship === '亲戚' || Relationship === 'Người thân' || Relationship === 'Relatives')) {
        return '亲戚'
      }
      if ((Relationship === '朋友' || Relationship === 'Bạn bè' || Relationship === 'Friend')) {
        return '亲戚'
      }
      if ((Relationship === '前同事' || Relationship === 'Đồng nghiệp cũ' || Relationship === 'Former colleague')) {
        return '前同事'
      }
      if ((Relationship === '同学' || Relationship === 'Bạn học' || Relationship === 'Classmate')) {
        return '同学'
      }
      if ((Relationship === '其它' || Relationship === 'Khác' || Relationship === 'Other')) {
        return '其它'
      }
    },
    // 工号change事件
    ReeNoChange () {
      GetIRMyMessage({
        type: 'General',
        empNo: this.Data.reeNo
      }).then(res => {
        if (res.status === 0 && res.data) {
          this.Data.reeName = res.data.NAME // 姓名
          this.Data.reeMobile = res.data.MOBILE // 推薦人聯繫方式
          this.Data.reePlant = res.data.PLANT // 推荐人厂区
          this.Data.reeDept = res.data.DEPTNAME // 推薦人部門
          this.Data.reeIdnumber = res.data.IDNUMBER // 推薦人身份證號碼
          this.Data.bank = res.data.BANK // 開卡銀行
          this.Data.bankNo = res.data.BANKNO // 銀行卡號
        } else {
          this.Data.reeName = '' // 姓名
          this.Data.reeMobile = '' // 推薦人聯繫方式
          this.Data.reeDept = '' // 推薦人部門
          this.Data.reeIdnumber = '' // 推薦人身份證號碼
          this.Data.bank = '' // 開卡銀行
          this.Data.bankNo = '' // 銀行卡號
        }
      })
    },
    // 日期选择器
    onConfirm (date) {
      this.Data.preDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
      this.showCalendar = false
    },
    // for ralAge
    onConfirm1 (date) {
      this.Data.ralAge = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
      this.showCalendar1 = false
    },
    // 动态更新选择器中的值
    SelectorClick (num) {
      switch (num) { // 与被推荐人关系
        case 'Data.relationship':
          this.columns = this.Relationship // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        case 'Data.reePlant':
          this.columns = this.reePlantArr // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        default:
      }
      this.showPicker = true // 打开选择器
    },
    // 选择器选中确定后的调用方法
    OnConfirm (value) {
      switch (this.TemporaryKey) {
        case 'Data.reePlant': // 推荐人厂区
          this.Data.reePlant = value
          break
        case 'Data.relationship': // 与被推荐人关系
          this.Data.relationship = this.MapLangRelationship(value)
          break
        default:
      }
      this.showPicker = false
    },
    // 提交推荐信息
    OnSubmit () {
      if (this.Data != null && this.Data.ralIdnumber.length > 12) {
        this.$toast(this.$t('推薦人身份證號碼不能超過12字'))
        return
      }
      AddIRGeneralWorker(this.Data).then(res => {
        if (res.status === 0) {
          this.$toast(this.$t('推荐成功'))
          this.$router.push({name: 'Recommend'})
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
 #GeneralWorkerRecommendation{
   height: 100%;
   background-color: white;
   .Content{
     height: calc(100% - 46px);
     .Referrer{
       h4{
         padding: 0;
         margin: 0;
         line-height: 40px;
         padding: 0 15px;
         color: burlywood;
         background-color: white;
       }
     }
   }
 }
</style>
<style lang="scss">
  #GeneralWorkerRecommendation {
    .van-cell {
      padding: 5px 16px;
      border-bottom: 1px solid #c8c9cc;
      .van-field__label {
        flex: 0.8;
      }
    }
    .RelationWorkedCss {
      .van-cell__title.van-field__label {
        flex: 2;
      }
    }
    .van-field__control {
      text-align: right;
    }
  }
</style>



