<template>
  <div id="Mine">
    <van-tabs v-model="activeName" animated swipeable @change="TabsChange">
      <van-tab :title="$t('我的收藏')" name="favourite">
        <van-pull-refresh class="ListCss" v-model="isLoading" @refresh="OnRefresh">
          <!--列表 上线的收藏单-->
          <div class="List" v-for="(item, index) in FavouriteArr" :key="index" @click="CollectListClick(item.id,'-1')">
            <div class="Title">
              <div><b>{{$t(item.Title)}}</b></div>
              <div class="Column">{{$t(item.Column)}}</div>
            </div>
            <div class="Time">{{item.Time}}</div>
          </div>
          <!-- 已下线的收藏单 -->
          <div v-if="FailFavouriteArr.length !== 0">
            <div class="FailListHint">{{$t('以下收藏单已下线，点击可取消收藏')}}</div>
            <div class="List" v-for="(item, index) in FailFavouriteArr" :key="index"
              @click="CollectListClick(item.id,index)" style="background: #9e9e9e7a;">
              <div class="Title">
                <div><b>{{$t(item.Title)}}</b></div>
                <div class="Column">{{$t(item.Column)}}</div>
              </div>
              <div class="Time">{{item.Time}}</div>
            </div>
          </div>
          <!--  -->
          <div v-if="FavouriteArr.length === 0 && FailFavouriteArr.length === 0" class="HintCss">
            <span>{{$t('暂无数据')}}</span>
          </div>
        </van-pull-refresh>
      </van-tab>
      <van-tab :title="$t('我的推荐')" name="recommand">
        <van-pull-refresh class="ListCss" v-model="isLoading" @refresh="OnRefresh">
          <!--列表-->
          <van-collapse :value="activeRecommand" @change="OnChange" v-if="RecommandArr.length !== 0">
            <van-collapse-item :name="index" class="CollapseList" v-for="(item, index) in RecommandArr" :key="index" @click="$toast(item.id)">
              <div slot="title">
                <div class="Title" v-if="item.show">
                  <div class="FlexLeftCenter">
                    <b>{{item.ReferralName}}</b>
                    <span class="Column">({{$t(item.Title)}})</span>
                    <div style="margin: 0 1rem;">
                      <van-button type="primary" size="mini" @click="CheckDetails('推荐', item.id)">{{$t('详情')}}</van-button>
                    </div>
                  </div>
                  <div class="Column">{{$t(item.Column)}}</div>
                </div>
                <div class="Title" v-else>
                  <div class="FlexLeftCenter">
                    <b>{{item.RalName}}</b>
                    <span class="Column">{{$t('(普工)')}}</span>
                    <div style="margin: 0 1rem;">
                      <van-tag :type="vanTagType(item.Status)" plain>{{ item.Status }}</van-tag>
                      <!-- <van-button type="primary" size="mini" @click="CheckDetails('推荐', item.id)">详情</van-button> -->
                    </div>
                  </div>
                  <div class="Column"> {{$t('推荐提交成功')}} </div>
                </div>
              </div>
              <van-steps :active="item.ActiveSteps" v-if="item.show"
                :active-icon="item.status == '1' ? 'replay' : item.status=='2' ? 'success' : 'cross'"
                :active-color="item.status == '1' ? '#ffaa00' : item.status=='2' ? '#38f' : '#ff5500'">
                  <van-step>{{$t('投递简历')}}</van-step>
                  <van-step>{{ item.status == '3' ? $t('面试失败') : $t('面试通过') }}</van-step>
                  <van-step>{{ item.status == '3' ? $t('任用失败') : $t('任用作业') }}</van-step>
                  <van-step>{{ item.status == '1' ? $t('入职作业') : item.status == '2' ? $t('入职成功') : $t('入职失败') }}</van-step>
              </van-steps>
              <div v-else>
                {{$t('推荐失败原因：')}}
                {{ item.FailReason || '/' }}
              </div>
            </van-collapse-item>
          </van-collapse>
          <div v-if="RecommandArr.length === 0" class="HintCss">
            <span>{{$t('暂无数据')}}</span>
            <span>{{$t('赶快到  职位列表 去推荐吧')}}</span>
          </div>
        </van-pull-refresh>
      </van-tab>
      <van-tab :title="$t('我的申请')" name="apply">
        <van-pull-refresh class="ListCss" v-model="isLoading" @refresh="OnRefresh">
          <!--列表-->
          <van-collapse :value="activeApply" @change="OnChange" v-if="ApplyArr.length !== 0">
            <van-collapse-item :name="index" class="CollapseList" v-for="(item, index) in ApplyArr" :key="index" @click="$toast(item.id)">
              <div slot="title">
                <div class="Title">
                  <div class="FlexLeftCenter">
                    <b>{{item.Title}}</b>
                    <div style="margin: 0 1rem;">
                      <van-button type="primary" size="mini" @click="CheckDetails('申请', item.id)">{{$t('详情')}}</van-button>
                    </div>
                  </div>
                  <div class="Column">{{item.Column}}</div>
                </div>
              </div>
              <van-steps :active="item.ActiveSteps" active-icon="success" active-color="#38f">
                <van-step>{{$t('投递简历')}}</van-step>
                <van-step>{{$t('面试通过')}}</van-step>
                <van-step>{{$t('任用作业')}}</van-step>
                <van-step>{{$t('入职成功')}}</van-step>
              </van-steps>
            </van-collapse-item>
          </van-collapse>
          <div v-if="ApplyArr.length === 0" class="HintCss">
            <span>{{$t('暂无数据')}}</span>
            <span>{{$t('赶快到  职位列表 去申请吧')}}</span>
          </div>
        </van-pull-refresh>
      </van-tab>
      <van-tab :title="$t('联系窗口')" name="ContactWindow">
        <div class="ContactWindowCss">
          <div class="flexCantter">
            <div class="title">{{$t('干部推荐异常联系窗口')}}</div>
            <div class="content">
              <div>{{$t('人资：')}}光氏賢</div>
              <div>{{$t('分机：')}}535-33743</div>
              <div>{{$t('邮箱：')}}cnsbg-hr-vn-rec-idl@mail.foxconn</div>
            </div>
            <div class="title">{{$t('普工推荐联系窗口')}}</div>
            <div class="content">
              <div>{{$t('人资：')}}黃氏戀</div>
              <div>{{$t('分机：')}}535-31882/097 6394655</div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
    <van-tabbar v-model="active" route>
      <van-tabbar-item icon="friends-o" to="/Recommend">{{$t('普工推荐')}}</van-tabbar-item>
      <van-tabbar-item icon="debit-pay" to="/">{{$t('干部推荐')}}</van-tabbar-item>
      <van-tabbar-item icon="manager-o" to="/Mine">{{$t('我的')}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {
  GetMyFavouriteList, // 查询
  SetMyFavourite, // 取消收藏
  GetIRRecommendStatus, // 获取推荐状态
  GetMyGeneralWorkerList // 手机端 获得我的普工推荐list
} from '@/model/api'
export default {
  name: 'Mine',
  data () {
    return {
      activeName: 'recommand', // 顶部导航默认打开 favourite
      active: 1, // 底部导航默认打开
      activeRecommand: [], // 我的推荐默认打开第一个折叠列表
      activeApply: [0], // 我的申请默认打开第一个折叠列表
      isLoading: false, // 刷新
      FavouriteArr: [], // 我的收藏
      FailFavouriteArr: [], // 我的收藏 > 失效
      RecommandArr: [], // 我的推荐
      ApplyArr: [] // 我的申请
    }
  },
  created () {},
  computed: {
    vanTagType () {
      return (type) => {
        let str = ''
        switch (type) {
          case '審核中':
            str = 'primary'
            break
          case '推薦成功':
            str = 'success'
            break
          case '推薦失敗':
            str = 'danger'
            break
        }
        return str
      }
    }
  },
  mounted () {
    this.InquireEvent()
  },
  methods: {
    OnChange (event) {
      this.activeRecommand = []
      this.activeRecommand = event
      this.activeApply = []
      this.activeApply = event
    },
    // 查詢
    InquireEvent () {
      GetMyFavouriteList({
        keyWords: this.activeName,
        page: 1,
        rows: 9999
      }).then(res => {
        if (res.status === 0) {
          switch (this.activeName) {
            case 'favourite': // 收藏
              this.FavouriteArr = []
              this.FailFavouriteArr = []
              // this.FavouriteArr = res.data.count
              res.data.records.map(item => {
                if (item.isUseFul === 1) {
                  this.FavouriteArr.push({
                    id: item.demandNo, // 需求单号
                    isUseFul: item.isUseFul, // 0表示已下线已过期等不可用需求，1表示可用需求
                    Title: item.positionName, // 岗位名称
                    Column: item.positionI, // 岗位大类
                    Time: item.publishDate // 发布时间
                  })
                } else if (item.isUseFul === 0) {
                  this.FailFavouriteArr.push({ // 失效的收藏单
                    id: item.demandNo, // 需求单号
                    isUseFul: item.isUseFul, // 0表示已下线已过期等不可用需求，1表示可用需求
                    Title: item.positionName, // 岗位名称
                    Column: item.positionI, // 岗位大类
                    Time: item.publishDate // 发布时间
                  })
                }
              })
              break
            case 'recommand': // 推荐
              this.RecommandArr = []
              res.data.records.map((item, index) => {
                this.RecommandArr.push({
                  id: item.id,
                  date: item.createDate, // 推荐时间
                  demandNo: item.demandNo,
                  Title: item.positionName,
                  Column: item.positionI,
                  Time: item.publishDate,
                  ReferralName: item.referralName,
                  ActiveSteps: 0,
                  status: 0,
                  show: true
                })
              })
              GetMyGeneralWorkerList({ Page: 1, Rows: 99999 }).then(res => {
                if (res.status === 0) {
                  res.data.records.map(item => {
                    this.RecommandArr.push({
                      date: item.reDate, // 推荐时间
                      RalName: item.ralName, // 被推荐人姓名
                      Status: item.status, // 推荐状态，推薦成功、推薦失敗、審核中
                      FailReason: item.failReason, // 失败原因。推荐失败时会有
                      show: false
                    })
                  })
                }
                this.RecommandArr.sort((a, b) => {
                  return a.date < b.date ? 1 : -1
                })
              })
              this.AssignRecommendStatus()
              break
            case 'apply': // 申请
              this.ApplyArr = []
              // this.ApplyArr = res.data.count
              res.data.records.map(item => {
                this.ApplyArr.push({
                  id: item.id,
                  demandNo: item.demandNo,
                  Title: item.positionName,
                  Column: item.positionI,
                  Time: item.publishDate,
                  ActiveSteps: 0,
                  status: 0
                })
              })
              break
            default:
          }
          this.isLoading = false
        }
      })
    },
    // 给推荐单的状态赋值
    // 推荐状态为0（简历投递）
    // 推荐状态为1（面试通过）
    // 推荐状态为2（面试未通过）
    // 推荐状态为3（任用作业）
    // 推荐状态为4（任用失败）
    // 推荐状态为5（入职作业）
    // 推荐状态为6（入职失败）
    // 推荐状态为7（入职成功）
    // 投递简历： 0
    // 面试通过： 1,2
    // 任用作业：3,4
    // 入职成功: 5,6,7
    // item.status 1:作业中， 2:成功，3:失败
    AssignRecommendStatus () {
      this.RecommandArr.map(async (item) => {
        let STATUS = item.id ? await this.GetRecommendStatus(item.id) : 999
        switch (STATUS) {
          case 0:
            item.ActiveSteps = 0
            item.status = 1
            break
          case 1:
            item.ActiveSteps = 1
            item.status = 2
            break
          case 2:
            item.ActiveSteps = 1
            item.status = 3
            break
          case 3:
            item.ActiveSteps = 2
            item.status = 1
            break
          case 4:
            item.ActiveSteps = 2
            item.status = 3
            break
          case 5:
            item.ActiveSteps = 3
            item.status = 1
            break
          case 6:
            item.ActiveSteps = 3
            item.status = 3
            break
          case 7:
            item.ActiveSteps = 3
            item.status = 2
            break
        }
      })
    },
    // 获取推荐单的状态
    GetRecommendStatus (id) {
      return Promise.resolve(
        GetIRRecommendStatus({
          recommendId: id
          // 'LTJ2022040800001'
        }).then(res => {
          return res.data.recommendStatus
        })
      )
    },
    // 查看详情
    CheckDetails (num, id) {
      switch (num) {
        case '推荐':
          this.$router.push({path: '/RecommendCheck', query: { Id: id }})
          break
        case '申请':
          this.$router.push({path: '/ApplicationCheck', query: { Id: id }})
          break
      }
    },
    // 标签改变时
    TabsChange () {
      this.InquireEvent()
    },
    // 刷新
    OnRefresh () {
      // setTimeout(() => {
      //   this.isLoading = false
      //   this.$toast('提示文案')
      // }, 1000)
      this.InquireEvent('favourite')
      this.$toast(this.$t('刷新成功'))
    },
    // 取消收藏
    CollectListClick (id, index) {
      if (index === '-1') { // 跳转岗位详情页面
        this.$router.push({path: '/JobDetails', query: { Id: id }})
        return
      }
      // 取消收藏
      SetMyFavourite({}, {
        demandNo: id
      }).then(res => {
        if (res.status === 0) {
          this.FailFavouriteArr.splice(index, 1)
          this.$toast('SetMyFavourite' + res.message)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  #Mine {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    background-color: #dcdcdc;
    .ListCss {
      height: calc(100% - 44px);
      padding: 10px;
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      background: #f4f8ff;
      // 正常列表样式
      .List {
        display: flex;
        background: #FFFFFF;
        border-radius: 5px;
        padding:10px;
        margin-bottom: 10px;
        box-shadow:0px 15px 10px -15px #000;
        .Title{
          line-height: 20px;
          text-align: left;
          color: #333333;
          .Column {
            font-size: 12px;
            color: #666666;
          }
        }
        .Time{
          line-height: 40px;
          margin-left: auto;
        }
      }
      // 折叠列表的样式
      .CollapseList {
        display: flex;
        flex-direction: column;
        background: #FFFFFF;
        border-radius: 5px;
        margin-bottom: 10px;
        box-shadow:0px 15px 10px -15px #000;
        .Title{
          font-size: 16px;
          line-height: 20px;
          text-align: left;
          color: #333333;
          .Column {
            font-size: 12px;
            color: #666666;
          }
        }
        .Time{
          line-height: 40px;
          margin-left: auto;
        }
      }
      .FailListHint {
        font-size: 12px;
      }
      .HintCss {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    .ContactWindowCss {
      height: calc(100% - 44px);
      padding: 10px;
      background: #f4f8ff;
      .flexCantter {
        height: 100%;
        background-color: #fff;
        .title {
          font-size: 26px;
          font-weight: 600;
          padding: 25% 0 10% 0;
          text-align: center;
        }
        .content {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          div {
            width: 90%;
            height: 2.5rem;
          }
        }
      }
    }
    .FlexLeftCenter {
      display: flex;
      align-items: center;
    }
  }
</style>
<style lang="scss">
  .van-tabs {
    height: calc(100% - 50px);
  }
  .van-tabs__content--animated {
    height: 100%;
  }
  .van-tab__pane, .van-tab__pane-wrapper {
    height: 100%;
  }
</style>



