<template>
  <div id="Recommend">
    <div class="Content">
      <div class="RichTextCss" v-html="RichText"></div>
     <div class="DivBtnCss">
       <van-button type="primary" round @click="RecommendClick">{{$t('立即申请')}}</van-button>
     </div>
    </div>
    <van-tabbar v-model="active" route>
      <van-tabbar-item icon="friends-o" to="/Recommend">{{$t('普工推荐')}}</van-tabbar-item>
      <van-tabbar-item icon="debit-pay" to="/">{{$t('干部推荐')}}</van-tabbar-item>
      <van-tabbar-item icon="manager-o" to="/Mine">{{$t('我的')}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {
  getWebNotice, // 获取公告
  GetContentById // 获取公告详情
} from '@/model/api'
export default {
  name: 'Recommend',
  data () {
    return {
      active: 1,
      RichText: '' // 富文本内容
    }
  },
  mounted () {
    getWebNotice({
      emp: this.$store.getters.getInfo.empno,
      realeaseType: '通知公告',
      showPage: '[香信端]普工推荐政策',
      site: 'GW,GZ,HT',
      page: 1,
      size: 10
    }).then(res => {
      console.log(res)
      if (res.Status === 0) {
        if (res.Data.list.length === 0) {
          this.$toast(this.$t('请在公告管理系统中发布最新的推荐政策'))
          return
        }
        GetContentById({
          rowId: res.Data.list[0].ROW_ID
        }).then(res => {
          console.log(res)
          if (res.Status === 0) {
            this.RichText = res.Data
          } else { this.$toast(res.Message) }
        })
      } else { this.$toast(res.Message) }
    })
  },
  methods: {
    RecommendClick () {
      // this.$router.push({name: 'RecommendDetails'})
      this.$router.push({path: '/GeneralWorkerRecommendation', query: {}})
    }
  }
}
</script>

<style scoped lang="scss">
  #Recommend {
    height: 100%;
    .Content{
      // height: calc(100% - 50px);
      background: #f4f8ff;
      // display: flex;
      .RichTextCss {
        min-height: 500px;
        padding: 1% 2%;
        // *:first-child {
        //   background: #0570DB !important;
        //   font-size: 16px;
        // }
        // p:nth-child(1) {
        //   background: #ff0000;
        //   font-size: 16px;
        // }
      }
      .DivBtnCss {
        padding-bottom: 80px;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        p {
          margin: 0px !important;
        }
      }
    }
  }
</style>
<style lang="scss">
  #Recommend {
    .van-button--normal {
      width: 80%;
      padding: 10px 15px;
      font-size: 16px;
    }
  }
</style>



