// 普工推荐
// https://appvn5.foxconn.com:8008/RMSOcelotService/rms/forward/rms/api/InternalRecruitment/Get_IR_MyMessage
export const AddIRGeneralWorker = p => put('rms/api/InternalRecruitment/Add_IR_GeneralWorker', p) // 普工推荐 》 推荐信息提交
export const GetIRMyMessage = p => get('rms/api/InternalRecruitment/Get_IR_MyMessage', p) // 获取我填单的历史信息
// 干部推荐
export const GetIRListPhone = p => post('rms/api/InternalRecruitment/Get_IRList_Phone', p) // 获得需求列表
export const GetIRBusinessoffice = p => get('rms/api/InternalRecruitment/Get_IR_Businessoffice', p) // 獲得需求崗位的詳細信息
// 干部推荐 > 崗位詳情
export const GetIRMessage = p => get('rms/api/InternalRecruitment/Get_IR_Message', p) // 獲得需求崗位的詳細信息
export const SetMyFavourite = (q, p) => post('rms/api/InternalRecruitment/Set_My_Favourite', q, p) // 收藏/取消收藏
// 干部推荐 > 岗位详情 > 推荐
export const AddIRRecommended = p => put('rms/api/InternalRecruitment/Add_IR_Recommended', p) // 提交推薦信息（提交、修改）
// 干部推荐 > 岗位详情 > 申请
export const AddIRApplyMessage = p => put('rms/api/InternalRecruitment/Add_IR_ApplyMessage', p) // 內部申請模塊（提交、修改）
// 我的
export const GetMyFavouriteList = p => post('rms/api/InternalRecruitment/Get_My_FavouriteList', p) // 获得我的收藏/推薦/申請列表
export const GetIRRecommendStatus = p => get('rms/api/InternalRecruitment/Get_IR_RecommendStatus', p) // 获取推荐单推荐状态
export const GetIRRecommandedMessage = p => get('rms/api/InternalRecruitment/Get_IR_RecommandedMessage', p) // 根據推薦編號獲取該單的詳細信息
export const GetIRApplyMessage = p => get('rms/api/InternalRecruitment/Get_IR_ApplyMessage', p) // 根據申請編號獲得詳細信息
export const GetMyGeneralWorkerList = p => post('rms/api/InternalRecruitment/GetMyGeneralWorkerList', p) // 手机端 获得我的普工推荐list

// 岗位推荐检查当前推荐人是否已被推荐
export const CheckRecCardId = p => get('rms/api/InternalRecruitment/Check_Rec_CardId', p) // 岗位详情/推荐

// 文件上传
export const uploadFilesWithAdd = p => post(postFileUrl + 'rmsfile/api/uploadFilesWithAdd', p) // 多文件上传(加入到同一个文件组)
export const uploadFiles = p => post(postFileUrl + 'rmsfile/api/uploadFiles', p) // 多文件上传(替换)
export const updFileStatus = (q, p) => put(postFileUrl + 'rmsfile/api/updFileStatus', q, p) // 启用文件组中的所有文件
export const delAllfiles = p => Delete(postFileUrl + 'rmsfile/api/delAllfiles', p) // 删除文件组中的所有文件
export const delFileByFileId = p => Delete(postFileUrl + 'rmsfile/api/delFileByFileId', p) // 删除文件id所代表的的文件
// link dung: https://rms-vn.cnsbg.efoxconn.com/forward/rmsfile/api/getFileInfo
export const getFileInfo = p => get(postFileUrl + 'rmsfile/api/getFileInfo', p) // 查询文件组中所有文件的信息

