<template>
   <div id='Inedx'>
      <van-nav-bar
        :title="$t('内部推荐')"
        left-text=""
        right-text=""
        left-arrow
        @click-left="$router.go(-1)"
        @click-right="{}"
      />
      <div>
        <!-- <div style="text-align: right !important;"><div style="width:30px; float: right;"><SelectLang/></div></div>
        -->
        <img src="../assets/1.png" style="width: 100%;"/>
      </div>
      <div class="flexCenter">
        <van-button type="info" to="Recommend" round>{{ $t('普工推荐') }}</van-button>
        <div style="height: 5%;"></div>
        <van-button type="info" @click="CadreRecommendation" round>{{ $t('干部推荐') }}</van-button>
      </div>
    </div>
</template>

<script>
// import SelectLang from '@/components/SelectLang.vue'
export default {
  components: {},
  data  () {
    return {

    }
  },
  created  () {

  },
  mounted  () {

  },
  computed: {},
  watch: {},
  methods: {
    CadreRecommendation () {
      this.$emit('CadreRecommendation', 1)
    }
  }
}
</script>
<style lang='scss' scoped>
  #Inedx {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    .title {
      font-size: 2rem;
      font-weight: bold;
      padding: 0 0 30% 0;
    }
    .flexCenter{
      height: calc(100% - 290px - 46px);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
</style>
<style lang='scss'>
  #Inedx {
    .van-nav-bar {
      background-color: #1989fa;
    }
    .van-nav-bar .van-icon {
      color: #f7f8fa;
    }
    .van-nav-bar__title {
      color: #f7f8fa;
      font-weight: bold;
    }
    .van-button--round {
      width: 80%;
      /* border-radius: 15px; */
    }
    .van-button--normal {
      /* padding: 30px 14px;
      font-size: 1.5rem; */
    }
  }
</style>



