import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index.js'
import Recommend from '@/pages/Recommend' // 普工推荐
import RecommendDetails from '@/components/RecommendAttached/RecommendDetails' // 普工推荐 > 推荐详情
import GeneralWorkerRecommendation from '@/components/RecommendAttached/GeneralWorkerRecommendation' // 普工推荐 > 推荐详情 > 申请列表

import JobFolder from '@/pages/JobFolder' // 干部推荐
import JobDetails from '@/components/JobFolderAttached/JobDetails' // 干部推荐>职位详情
import JobApplication from '@/components/JobFolderAttached/JobApplication' // 干部推荐>职位详情>职位申请
import BasicInformation from '@/components/JobFolderAttached/BasicInformation' // 干部推荐>职位详情>职位申请>信息编辑
import JobRecommend from '@/components/JobFolderAttached/JobRecommend' // 干部推荐>职位详情>职位推荐>信息编辑
import TipText from '@/components/TipText' // 提交完成信息提示页面

import Mine from '@/pages/Mine' // 我的
import RecommendCheck from '@/components/MineAttached/RecommendCheck' // 我的>推荐详情
import ApplicationCheck from '@/components/MineAttached/ApplicationCheck' // 我的>申请详情

import Index from '@/pages/Index' // 索引页
import FailedToLogin from '@/pages/FailedToLogin' // 登陆失败
import axios from 'axios'

Vue.use(Router)

const router = new Router({
  routes: [
    { // 索引页
      path: '/Index',
      name: 'Index',
      component: Index
    },
    { // 登陆失败
      path: '/FailedToLogin',
      name: 'FailedToLogin',
      component: FailedToLogin
    },
    { // 普工推荐
      path: '/Recommend',
      name: 'Recommend',
      component: Recommend
    },
    { // 普工推荐 > 推荐详情
      path: '/RecommendDetails',
      name: 'RecommendDetails',
      component: RecommendDetails
    },
    { // 普工推荐 > 推荐详情 > 申请列表
      path: '/GeneralWorkerRecommendation',
      name: 'GeneralWorkerRecommendation',
      component: GeneralWorkerRecommendation
    },
    { // 干部推荐
      path: '/',
      name: 'JobFolder',
      component: JobFolder,
      meta: {
        keepAlive: true // 缓存页面数据到内存里
      }
    },
    { // 干部推荐>职位详情
      path: '/JobDetails',
      name: 'JobDetails',
      component: JobDetails
    },
    { // 干部推荐>职位详情>职位申请
      path: '/JobApplication',
      name: 'JobApplication',
      component: JobApplication
    },
    { // 干部推荐>职位详情>职位申请>信息编辑
      path: '/BasicInformation',
      name: 'BasicInformation',
      component: BasicInformation
    },
    { // 干部推荐>职位详情>职位推荐>信息编辑
      path: '/JobRecommend',
      name: 'JobRecommend',
      component: JobRecommend
    },
    { // 提交完成信息提示页面
      path: '/TipText',
      name: 'TipText',
      component: TipText
    },
    { // 我的
      path: '/Mine',
      name: 'Mine',
      component: Mine
    },
    { // 我的>推荐单详情
      path: '/RecommendCheck',
      name: 'RecommendCheck',
      component: RecommendCheck
    },
    { // 我的>申请单详情
      path: '/ApplicationCheck',
      name: 'ApplicationCheck',
      component: ApplicationCheck
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 测试环境
  if (window.location.origin.indexOf('localhost') > -1 || window.location.origin.indexOf('8017') > -1) {
    store.commit('update', {
      Info: {
        empno: 'F1338096',
        Token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..8kUxX-86aXdF8z6wKfz1QNohJsFMxYPH3HNeiAg4Fcw'
      }
    })
    next()
    return
  }
  // 正式环境加是否是信息环境的判断
  if (window.location.origin.indexOf('https://microapps.foxconn.com') > -1) {
    let useCivet = window.cm.useCivet
    if (useCivet === true) {
      console.log('是相信环境')
    } else {
      console.log('不是相信环境')
      window.location.replace('https://microapps.foxconn.com/denyVisite/salaryEnquiry/404.html')
      return
    }
  }
  // 已登陆
  if (to.fullPath.indexOf('FailedToLogin') > -1) {
    next()
  }
  // 未登陆
  if (!store.getters.getInfo.empno) {
    if (to.query.empno || to.query.code) {
      // 获取 Token
      let Token = ''
      GetToken({
        empNo: 'F1338096',
        UserName: 'IT',
        PassWord: 'Web'
      }).then(res => {
        // this.$toast(res)
        if (res.Status === 0) { Token = res.Data }
      })
      // 香信登入
      if (to.query.empno) {
        let obj = {
          Info: {
            empno: to.query.empno,
            name: to.query.name,
            // Token: Token
            Token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..8kUxX-86aXdF8z6wKfz1QNohJsFMxYPH3HNeiAg4Fcw'
          }
        }
        store.commit('update', obj)
        next()
        return
      }
      // 扫码登陆
      if (to.query.code) {
        GetInfo({
          code: to.query.code
        }).then(res => {
          let obj = {
            Info: {
              empno: res.civetno,
              name: res.realname,
              Token: Token
            }
          }
          store.commit('update', obj)
          next()
        })
      }
    } else {
      // 未获取到工号
      next('/FailedToLogin')
    }
  } else {
    // 已登陆
    next()
  }
})

export function GetToken (params) {
  return axios({
    url: 'https://microapps.foxconn.com/ocelotservice/announcementtoken/api/privilege/getToken',
    method: 'get',
    params: params
  }).then(res => {
    if (res.status === 200) {
      return res.data
    }
  })
}

export function GetInfo (params) {
  return axios({
    url: 'https://microapps.foxconn.com/smartoffice/api/SmartOffice/GetCiVetUserInfo',
    method: 'get',
    params: params
  }).then(res => {
    if (res.status === 200) {
      return res.data
    }
  })
}

export default router



