<template>
  <div id="RecommendCheck">
    <van-nav-bar
      :title="$t('推荐详情')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />
    <div class="Content">
      <div class="Title">{{$t('推荐人基本信息')}}</div>
      <table cellpadding="0" cellspacing="0">
        <tr>
          <td rowspan="3" width="60px">
            <div>
              <van-icon name="user-circle-o" color="#00aaff"  />
            </div>
          </td>
          <td>
            <div class="FlexLeft">
              <span>{{ refereesData.name }}</span>
              ({{ refereesData.empNo }})
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ refereesData.deptName }}
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ refereesData.classPosition }}
              ({{ refereesData.managePosition }})
              ({{ refereesData.positionName }})
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('推荐编号：')}}{{ refereesData.recommendId }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2" style="border-bottom: none;">
            <div class="FlexLeft">
              {{$t('与被推荐人关系：')}}{{ refereesData.relationship }}
            </div>
          </td>
        </tr>
      </table>
      <div class="Title">{{$t('被推荐人基本信息')}}</div>
      <table cellpadding="0" cellspacing="0">
        <tr>
          <td rowspan="3" width="60px">
            <div>
              <van-icon name="user-circle-o" color="#55ff7f"  />
            </div>
          </td>
          <td>
            <div class="FlexLeft">
              <span>{{ referralData.name }}</span>
              ({{ referralData.age }}岁)
              {{referralData.mobile}}
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ referralData.idNumber }}
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ referralData.qualification }} |
              {{ referralData.major }} |
              {{ referralData.school }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('推荐职位：')}}{{ referralData.positionName }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('工作特长：')}}
              {{ referralData.skills }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('被推荐人员富士康工作记录：')}}
              {{ referralData.isworked ==  '1' ? $t('是') : $t('否') }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('被推薦人是否有其它親友就職于富士康：')}}
              {{ referralData.relationWorked ==  '1' ? $t('是') : $t('否') }}
            </div>
          </td>
        </tr>
        <tr v-if="referralData.relationWorked ==  '1'">
          <td colspan="2">
            <table cellpadding="0" cellspacing="0" style="border: none;">
              <tr>
                <td>{{$t('姓名')}}</td>
                <td>{{$t('工号')}}</td>
                <td>{{$t('服务单位')}}</td>
                <td style="border-right: none;">{{$t('关系')}}</td>
              </tr>
              <tr v-for="(item, index) in referralData.referralRelations" :key="index">
                <td style="border-bottom: none;">{{ item.name }}</td>
                <td style="border-bottom: none;">{{ item.empNo }}</td>
                <td style="border-bottom: none;">{{ item.deptName }}</td>
                <td style="border: none;">{{ item.relationship }}</td>
              </tr>
            </table>
          </td>
        </tr>
        <tr v-if="FileArr.length !== 0">
          <td colspan="2" style="border-bottom: none;">
            <div v-for="(item, index) in FileArr" :key="index">
              <!-- <span @click='FileClick(item.url)' style="color: #6337b6;cursor: pointer;">{{ item.name }}</span> -->
              <!-- <a :href="item.url.replace('http://10.167.214.11:8003/', 'https://microapps.foxconn.com/ocelotservice/rms/forward/rmsfile')"
               :download="item.name" style="color: #6337b6;cursor: pointer;">{{ item.name }}</a>
               -->
               <a :href="item.url.replace('http://10.167.214.11:8003/', 'https://appvn5.foxconn.com:8008/RMSOcelotService/rmsfile/rmsfile')"
               :download="item.name" style="color: #6337b6;cursor: pointer;">{{ item.name }}</a>
            </div>
          </td>
        </tr>
      </table>
      <div class="bottomBtnCss">
        <van-button type="info" @click="$router.go(-1)" round style="width: 100%;">{{$t('返回')}}</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  GetIRRecommandedMessage, // 獲得需求崗位的詳細信息
  getFileInfo // 获取文件
} from '@/model/api'
export default {
  name: 'RecommendCheck',
  data () {
    return {
      FileArr: [], // 文件
      refereesData: {}, // 推荐人信息
      referralData: {} // 被推荐人信息
    }
  },
  mounted () {
    // console.log(this.$route.query.Id)
    GetIRRecommandedMessage({
      recommandedId: this.$route.query.Id
    }).then(res => {
      this.getIRRecommandedMessageres = JSON.stringify(res)
      if (res.status === 0) {
        this.refereesData = res.data.referees
        this.referralData = res.data.referral
        if (this.referralData.fileId) {
          this.getFile()
        }
      }
    })
  },
  methods: {
    // 获取文件
    getFile () {
      getFileInfo({
        groupId: this.referralData.fileId
      }).then(res => {
        if (res.code === 0) {
          this.FileArr = []
          res.data.fileList.map(item => {
            let suffix = item.filePath.split('.')
            this.FileArr.push({
              id: item.fileId,
              name: item.fileName + '.' + suffix[suffix.length - 1],
              url: item.filePath
            })
          })
        }
      }).catch(e => {
        console.log(e)
      })
    },
    // 文件打开
    FileClick (url) {
      // url = url.replace('http://10.167.214.11:8003/', 'https://microapps.foxconn.com/ocelotservice/rms/forward/rmsfile')
      url = url.replace('http://10.167.214.11:8003/', 'https://appvn5.foxconn.com:8008/RMSOcelotService/rmsfile/rmsfile')
      window.open(url, '_blank')
      console.log(url)
    }
  }
}
</script>

<style scoped lang="scss">
  #RecommendCheck{
    background-color: white;
    text-align: left;
    height: 100%;
    .Content{
      padding: 0 1rem;
      height: calc(100% - 97px);
      overflow-x: hidden;
      overflow-y: auto;
      .Title {
        font-size: 15px;
        font-weight: bolder;
        margin: 1rem 0 0.2rem 0;
      }
      table {
        width: 100%;
        border: 0.2px solid #a6a6a6;
        border-right: none;
        tr {
          height: 25px;
          td {
            min-width: 40px;
            line-height: 25px;
            font-size: 13px;
            text-align: center;
            border-right: 0.2px solid #a6a6a6;
            border-bottom: 0.2px solid #a6a6a6;
            border-collapse: collapse;
            box-sizing: border-box;
            div {
              display: flex;
              justify-content: center;
              align-items: center;
            }
            .FlexLeft {
              justify-content: flex-start;
              padding: 0 0 0 1rem;
              span {
                font-size: 1rem;
                padding: 0 0.2rem 0 0 ;
                font-weight: 600;
              }
            }
          }
        }
      }
      .bottomBtnCss {
        margin-top: 2rem;
        display: flex;
        justify-content: space-around;
      }
    }
  }
</style>
<style lang="scss">
  #RecommendCheck {
    table {
      .van-icon {
        font-size: 3.5rem;
      }
    }
  }
</style>



