<template>
  <div id="JobRecommend">
    <van-nav-bar
      :title="$t('职位推荐')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />
    <div class="Content">
      <!--推荐人-->
      <div class="Referrer">
        <van-form @submit="OnSubmit">
          <h4>{{ $t('推荐人信息') }}</h4>
          <van-field v-model="Referees.empNo" :label="$t('工号')" :placeholder="$t('请输入工号')" :rules="[{ required: true }]" input-align="right" @change="EmpNoChange"/>
          <van-field v-model="Referees.name" :label="$t('姓名')" :placeholder="$t('请输入姓名')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.positionName" :label="$t('岗位')" :placeholder="$t('请输入岗位')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.deptName" :label="$t('服务部门')" :placeholder="$t('请输入服务部门')" :rules="[{ required: true }]"
            type="textarea" maxlength="50" :autosize="{ maxHeight: 50, minHeight: 50 }" show-word-limit></van-field>
          <van-field v-model="Referees.idNumber" :label="$t('身份证号')" :placeholder="$t('请输入身份证号')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.address" :label="$t('户籍')" :placeholder="$t('请输入户籍')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.classPosition" :label="$t('资位')" :placeholder="$t('请输入资位')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.managePosition" :label="$t('管理职')" :placeholder="$t('请输入管理职')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referees.mobile" :label="$t('联系方式')" :placeholder="$t('请输入联系方式')" :rules="[{ required: true }]" input-align="right"/>
          <van-field :value="Referees.relationship" :label="$t('与被推荐人关系')" :placeholder="$t('请选择与被推荐人关系')" @click="SelectorClick('Referees.relationship')"
            readonly clickable name="picker" :rules="[{ required: true }]"/>

          <div class="ReferralCss">
            <h4>{{ $t('被推荐人信息') }}</h4>
          </div>

          <div class="flexDiv">
            <div class="title">{{ $t('上传简历') }}</div>
            <van-uploader @click="FileUpload()" :after-read="FileUpload" accept=".pdf,.jpg,.png" name="uploadFile" style="flex: 1;">
              <van-button :icon="FileName === '请上传被推荐人简历' ? 'plus' : 'success'" type="primary" round>{{ FileName }}</van-button>
            </van-uploader>
            <div style="margin: 0 0.3rem;" @click="DeleteFile">
              <van-icon name="delete-o" color="#ee0a24"/>
            </div>
            <div class="hint">
              {{ $t('上传简历按钮点击无反应时请将香信更新至最新版本') }}
            </div>
          </div>

          <van-field v-model="Referral.name" :label="$t('姓名')" :placeholder="$t('请输入姓名')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referral.idNumber" :label="$t('身份证号')" :placeholder="$t('请输入身份证号')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referral.age" :label="$t('年龄')" :placeholder="$t('请输入年龄')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referral.mobile" :label="$t('联系方式')" :placeholder="$t('请输入联系方式')" :rules="[{ required: true }]" input-align="right"/>
          <van-field :value="Referral.qualification" :label="$t('学历')" :placeholder="$t('请选择学历')" @click="SelectorClick('Referral.qualification')"
            readonly clickable name="picker" :rules="[{ required: true }]"/>
          <van-field v-model="Referral.school" :label="$t('毕业学校')" :placeholder="$t('请输入毕业学校')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-model="Referral.major" :label="$t('专业')" :placeholder="$t('请输入专业')" :rules="[{ required: true }]" input-align="right"/>
          <van-field :value="Referral.graduateDate" :label="$t('毕业时间')" :placeholder="$t('请选择毕业时间')" @click="showDate.show = true"
            readonly clickable name="picker" :rules="[{ required: true }]"/>
          <van-field :value="Referral.isworked" :label="$t('是否有富士康工作记录')" :placeholder="$t('请选择是否有富士康工作记录')" @click="SelectorClick('Referral.isworked')"
            readonly clickable name="picker" :rules="[{ required: true }]"/>
          <van-field v-model="Referral.skills" :label="$t('工作特长')" :placeholder="$t('请简述工作特长')" :rules="[{ required: true }]"
            type="textarea" maxlength="50" :autosize="{ maxHeight: 50, minHeight: 50 }" show-word-limit></van-field>
          <van-field :value="Referral.relationWorked" :label="$t('被推荐人是否有其它亲友就职于富士康')" :placeholder="$t('请选择是否有其它亲友就职于富士康')" @click="SelectorClick('Referral.relationWorked')"
            readonly clickable name="picker" class="RelationWorkedCss" :rules="[{ required: true }]"/>

          <van-field v-if="Referral.relationWorked === '是'" v-model="Referral.referralRelations[0].name" :label="$t('姓名')" :placeholder="$t('请输入姓名')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-if="Referral.relationWorked === '是'" v-model="Referral.referralRelations[0].empNo" :label="$t('工号')" :placeholder="$t('请输入工号')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-if="Referral.relationWorked === '是'" v-model="Referral.referralRelations[0].deptName" :label="$t('服务单位')" :placeholder="$t('请输入服务单位')" :rules="[{ required: true }]" input-align="right"/>
          <van-field v-if="Referral.relationWorked === '是'" v-model="Referral.referralRelations[0].relationship" :label="$t('关系')" :placeholder="$t('请输入关系')" :rules="[{ required: true }]" input-align="right"/>

          <van-collapse v-model="activeNames" style="padding: 25px 0;">
            <van-collapse-item name="1">
              <template #title>
                <van-checkbox v-model="Checked" checked-color="#ee0a24" style="border: none;">
                  {{ $t('本人自願為') }}<span><u>&nbsp;&nbsp;{{Referral.name}}&nbsp;&nbsp;</u></span>{{ $t('擔保﹐保證上述資料真實可信，若隱瞞該員不良記錄而造成公司損失﹐經查實﹐本人願受連帶處分。') }}
                </van-checkbox>
              </template>
              {{ $t('1.確保被推薦人員工作經歷及証件真實(附上個人履歷及相關証件復印件) 必備証件﹕身份証﹑畢業証。') }}<br />
              {{ $t('2.確保被推薦人員無不良品行。') }}
            </van-collapse-item>
          </van-collapse>
          <!-- 时间格式展开选择器 -->
          <DatetimePickerModule :ModelContent='showDate' @GetDate="GetDateClick"></DatetimePickerModule>
          <!-- 数组展开选择器 -->
          <van-popup v-model="showPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="columns"
              @confirm="OnConfirm"
              @cancel="showPicker = false"
            />
          </van-popup>

          <div style="margin: 16px;">
            <van-button round block type="primary" native-type="submit" :loading="loadingBtn" loading-type="spinner">{{ $t('提交') }}</van-button>
          </div>
        </van-form>
      </div>
    </div>

  </div>
</template>

<script>
import DatetimePickerModule from '../DatetimePickerModule.vue'
import {
  CheckRecCardId, // 检查当前被推荐人是否已被推荐
  AddIRRecommended, // 提交推荐信息
  GetIRMyMessage, // 获取我填单的历史信息
  uploadFiles, // 上传文件，获取文件的组id
  updFileStatus, // 启用文件组中的所有文件
  uploadFilesWithAdd, // 更新文件组中的文件
  delAllfiles // 删除文件组中所有的文件
} from '@/model/api'
export default {
  name: 'JobRecommend',
  data () {
    return {
      showPicker: false, // 选择器是否打开
      showDate: { // 时间选择器是否打开
        show: false
      },
      loadingBtn: false,
      columns: [], // 选择器数组
      activeNames: ['0'], // 底部折叠面板
      Checked: false, // 是否同意担保
      TemporaryKey: '', // 选择器临时中转的key值
      FileName: '请上传被推荐人简历', // 上传的文件名字
      Relationship: ['亲戚', '朋友', '前同事', '同学', '其它'], // 与被推荐人关系
      IsworkedArr: ['是', '否'], // 是否有富士康工作记录
      RelationWorkedArr: ['是', '否'], // 被推荐人是否有其它亲友就职于富士康
      EducationArr: ['博士后', '博士', '硕士', 'EMBA/MBA', '本科', '大专', '高中', '高技', '中职', '初中及以下'], // 学历
      Referees: { // 推荐人信息
        recommendId: '', // 推薦編號。新增时不传，更新时以此ID为准。
        name: '', // 推薦人姓名
        empNo: '', // 推薦人工號
        positionName: '', // 推薦人職位名稱
        deptName: '', // 推薦人部門
        idNumber: '', // 推薦人身份證
        address: '', // 推薦人戶籍
        classPosition: '', // 推薦人資位
        managePosition: '', // 推薦人管理職
        mobile: '', // 推薦人聯繫方式
        relationship: '' // 與被推薦人的關係
      },
      Referral: { // 被推荐人信息
        recommendId: '', // 推薦編號
        demandNo: this.$route.query.Id, // 需求單號
        talentNo: '', // 人才編號
        name: '', // 被推薦人姓名
        idNumber: '', // 被推薦人身份證
        age: '', // 被推薦人年齡
        mobile: '', // 被推薦人聯繫方式
        qualification: '', // 被推薦人學歷
        school: '', // 被推薦人畢業院校
        major: '', // 被推薦人專業
        graduateDate: '', // 被推薦人畢業時間
        isworked: '', // 是否在富士康工作過,0沒有，1有。默認沒有
        skills: '', // 被推薦人工作特長
        relationWorked: '', // 是否有親友在富士康工作,0沒有，1有。默認沒有
        recomStatus: '', // 推薦狀態。招募專員審核中 1，招募專員結案 2，人資主管審核中 3，人資主管審核通過 4，人資主管結案 5
        fileId: '', // 上傳文件編號
        imNo: '', // 面试单号
        rcom: '', // 招募专员
        hrMajor: '', // 人资主管
        referralRelations: [ // 被推薦人在FOXCONN工作的親友信息
          {
            recommendId: '', // 推薦ID
            name: '', // 親屬姓名
            empNo: '', // 親屬工號
            deptName: '', // 親屬部門
            relationship: '' // 與被推薦人的關係
          }
        ]
      }
    }
  },
  components: {
    DatetimePickerModule
  },
  watch: {
    'Referral.idNumber': function () {
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
      if (reg.test(this.Referral.idNumber) === true) {
        let myDate = new Date()
        let year = myDate.getFullYear()
        let birthDay = this.Referral.idNumber.slice(6, 10)
        this.Referral.age = year - birthDay
      }
    }
  },
  created () {
    // 获取rms系统中配置的基础数据，接口需要Token，暂无法使用
    // this.$store.dispatch('HttpChildNode', 'Education').then(res => { // 學歷
    //   this.EducationArr = res
    // })
  },
  mounted () {
    this.Referees.empNo = this.$store.getters.getInfo.empno
    this.EmpNoChange()
  },
  methods: {
    // 时间选择器的方法
    GetDateClick (date) {
      this.Referral.graduateDate = date
    },
    // 推荐人工号change事件
    EmpNoChange () {
      GetIRMyMessage({
        type: 'Rec',
        empNo: this.Referees.empNo
      }).then(res => {
        if (res.status === 0 && res.data) {
          this.Referees.name = res.data.NAME // 推薦人姓名
          this.Referees.positionName = res.data.POSITIONNAME // 推薦人職位名稱
          this.Referees.deptName = res.data.DEPTNAME // 推薦人部門
          this.Referees.idNumber = res.data.IDNUMBER // 推薦人身份證
          this.Referees.address = res.data.ADDRESS // 推薦人戶籍
          this.Referees.classPosition = res.data.CLASSPOSITION // 推薦人資位
          this.Referees.managePosition = res.data.MANAGEPOSITION // 推薦人管理職
          this.Referees.mobile = res.data.MOBILE // 推薦人聯繫方式
        }
      })
    },
    // 动态更新选择器中的值
    SelectorClick (num) {
      switch (num) { // 与被推荐人关系
        case 'Referees.relationship':
          this.columns = this.Relationship // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        case 'Referral.isworked':
          this.columns = this.IsworkedArr // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        case 'Referral.relationWorked':
          this.columns = this.RelationWorkedArr // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        case 'Referral.qualification':
          this.columns = this.EducationArr // 给选择器赋值
          this.TemporaryKey = num // 临时储存key值
          break
        default:
      }
      this.showPicker = true // 打开选择器
    },
    // 选择器选中确定后的调用方法
    OnConfirm (value) {
      switch (this.TemporaryKey) { // 与被推荐人关系
        case 'Referees.relationship':
          this.Referees.relationship = value
          break
        case 'Referral.isworked':
          this.Referral.isworked = value
          break
        case 'Referral.relationWorked':
          this.Referral.relationWorked = value
          break
        case 'Referral.qualification':
          this.Referral.qualification = value
          break
        default:
      }
      this.showPicker = false
    },
    // 文件上传功能
    FileUpload (file) {
      this.$toast(this.$t('文件上传'))
      // 文件上传
      var FileFormData = new FormData() // 处理需要上传文件
      FileFormData.append('files', file.file) // 文件
      FileFormData.append('emp', this.$store.getters.getInfo.empno) // 工号
      FileFormData.append('fileDesc', this.$store.getters.getInfo.empno + '推荐') // 文件描述
      if (!this.Referral.fileId) { // 判断当前需求单是否绑定了组id
        uploadFiles(FileFormData).then(res => { // 上传文件，获取文件组id
          if (res.code === 0) {
            this.Referral.fileId = res.data
            this.FileName = file.file.name
            this.$toast(this.$t('上传成功'))
          } else {
            this.$toast('uploadFiles' + res.msg)
          }
        })
      } else {
        FileFormData.append('groupId', this.Referral.fileId) // 文件组(必传)
        // 删除文件组中的所有文件
        delAllfiles({
          groupId: this.Referral.fileId
        }).then(res => {
          if (res.code === 0) {
            // 上传文件
            uploadFilesWithAdd(FileFormData).then(res => {
              if (res.code === 0) {
                this.$toast(this.$t('上传成功'))
              }
            })
          }
        })
      }
      // window.cm.openFile({
      //   type:type,  // 文件类型
      //   url:url,  // 文件路径
      //   deal: function (url) {
      //     // 在非香信环境下处理方法
      //     // 参数url：当前文件的url
      //   }
      // })
    },
    // 删除文件
    DeleteFile () {
      this.FileName = '请上传被推荐人简历'
      this.Referral.fileId = ''
      this.$toast(this.$t('删除成功'))
    },
    // 提交推荐信息
    OnSubmit () {
      if (this.loadingBtn) { return }
      this.loadingBtn = true
      setTimeout(() => {
        this.loadingBtn = false
      }, 1000)
      let obj = {}
      obj.referees = JSON.parse(JSON.stringify(this.Referees)) // 数据深拷贝
      obj.referral = JSON.parse(JSON.stringify(this.Referral)) // 数据深拷贝
      obj.referral.isworked = obj.referral.isworked === '是' ? 1 : 0
      obj.referral.relationWorked = obj.referral.relationWorked === '是' ? 1 : 0
      if (this.FileName === '请上传被推荐人简历') {
        this.$toast(this.$t('请上传被推荐人简历'))
        return
      }
      if (!this.Checked) {
        this.$toast(this.$t('请确认是否给被推荐人担保'))
        return
      }
      // 检查当前被推荐人是否已被推荐
      CheckRecCardId({
        demandNo: this.$route.query.Id,
        cardId: obj.referral.idNumber
      }).then(item => {
        if (item.status === 0 && !item.data) {
          // 提交数据
          AddIRRecommended(obj).then(res => {
            if (res.status === 0) {
              this.$toast(this.$t('推荐成功'))
              if (this.Referral.fileId) {
                // 启用文件
                updFileStatus({}, { // 启用文件组中的所有文件
                  fileGroup: this.Referral.fileId
                }).then(res1 => {
                  if (res1.code === 0) {
                  } else {
                    this.$toast('updFileStatus' + res1.msg)
                  }
                })
              }
              this.$router.push({name: 'JobFolder'})
            } else {
              this.$toast('AddIRRecommended' + res.message)
            }
          })
        } else if (item.status !== 0) {
          this.$toast('CheckRecCardId' + item.message)
        } else if (item.data) {
          this.$toast(this.$t('当前被推荐人已在招募流程中'))
        }
        // this.loadingBtn = false
      })
    }
  }
}
</script>

<style scoped lang="scss">
 #JobRecommend{
   height: 100%;
   background-color: white;
   .Content{
     height: calc(100% - 46px);
     .Referrer{
       h4{
         padding: 0;
         margin: 0;
         line-height: 40px;
         padding: 0 10px;
         color: burlywood;
         background-color: white;
       }
       .ReferralCss {
         display: flex;
         align-items: center;
       }
       .flexDiv {
         padding: 5px 16px;
         border-top: 1px solid #c8c9cc;
         border-bottom: 1px solid #c8c9cc;
         display: flex;
         flex-wrap: wrap;
         justify-content: space-between;
         align-items: center;
         .title {
            width: 70px;
            font-size: 14px;
         }
         .hint {
           width: 100%;
           font-size: 12px;
           color: #ee0a24;
           margin-top: 5px;
           border: 1px dotted #828282;
         }
       }
     }
   }
 }
</style>
<style lang="scss">
  #JobRecommend {
    .van-cell {
      padding: 5px 16px;
      border-bottom: 1px solid #c8c9cc;
      .van-field__label {
        flex: 0.8;
      }
    }
    .RelationWorkedCss {
      .van-cell__title.van-field__label {
        flex: 2;
      }
    }
    .flexDiv {
      .van-button--normal {
          padding: 1px 8px;
          font-size: 12px;
      }
      .van-button {
        height: 100%;
      }
    }
    .van-field__control {
      text-align: right;
    }
  }
</style>



