<template>
  <div id="JobApplication">
    <div class="TopFixed">
      <van-nav-bar
        :title="$t('职位申请')"
        :left-text="$t('返回')"
        left-arrow
        @click-left=" $router.go(-1)"
      />
    </div>
    <div class="FormBody">
      <div class="Basic_Information">
        <div v-if="PersonalSql.start">
          <div>
            <span class="name"><b>{{PersonalSql.data.name}}</b></span>
            <span>({{PersonalSql.data.empNo}})</span>
            <span>{{PersonalSql.data.classPosition}}</span>
            <span class="BasicButton" @click="routerPushClick('Personal',Personal,[2,-1])"> <span>{{ $t('编辑') }}</span> <van-icon name="arrow" /> </span>
          </div>
          <div><span>{{ $t('身份证号：') }}</span>{{PersonalSql.data.idnumber}}</div>
          <div><span>{{ $t('当前部门：') }}</span>{{PersonalSql.data.cudeptName}}</div>
        </div>
        <div v-else style="height: 3rem;text-align: center;line-height: 3rem;color: #9FB6CD" @click="routerPushClick('Personal',Personal,[2,-1])">
          {{ $t('个人基本信息待编辑(点我编辑)') }}
        </div>
      </div>
      <!--多项填写-->
      <div class="Multiple" v-for="(item, key) in MultipleEdits" :key="key">
        <div class="Title">
          <b>{{item.title}}</b>
          <span class="BasicButton" @click="routerPushClick(key,item,[0,0])"> <span>{{ $t('添加') }}</span> <van-icon name="plus" /> </span>
        </div>
        <div v-if="$store.getters['get'+key].ListData.length < 1" style="height: 3rem;text-align: center;line-height: 3rem;color: #9FB6CD">{{ $t('待添加') }}</div>
        <van-cell v-else :value="$t('编辑')" is-link  v-for="(data, index) in $store.getters['get'+key].ListData" :key="index" @click="showPopup($store.getters['get'+key].ListData[index].ListTile,key,item,index)">
          <!-- 使用 title 插槽来自定义标题 -->
          <template #title>
            <span class="custom-title">{{$t(data.ListTile)}}</span>
            <van-tag type="success">{{$t(data.label)}}</van-tag>
          </template>
        </van-cell>
      </div>
      <!--内调原因-->
      <div class="ReasonTransfer">
        <div class="Title">
          <b>{{ $t('内调原因')}}</b>
        </div>
        <van-field
          v-model="ReasonForTransfer"
          rows="2"
          autosize
          :label="$t('留言')"
          type="textarea"
          maxlength="100"
          :placeholder="$t('请输入留言')"
          show-word-limit
        />
      </div>
      <div class="FileBtnCss">
        <van-uploader @click="FileUpload()" :after-read="FileUpload" accept=".pdf,.jpg,.png">
          <van-button :icon="FileName === '上传文件' ? 'plus' : 'success'" type="primary">{{ FileName }}</van-button>
        </van-uploader>
        <span style="margin: 0 0.3rem;" @click="DeleteFile">
          <van-icon name="delete-o" color="#ee0a24"/>
        </span>
      </div>
      <div style="border: 2px dotted #828282;margin: 0 10px;font-size: 12px;color: #ee0a24;">
       {{ $t('上传文件按钮点击无反应时请将香信更新至最新版本') }}
      </div>
      <div style="padding: 0.5rem 0.7rem">
        <van-button type="primary" block @click="SubmitApplication">{{ $t('提 交 申 请') }}</van-button>
      </div>
    </div>
    <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
      <div style="padding: 1rem">
        <div style="text-align: center"><b>{{DataAlert.title}}</b></div>
        <van-button type="primary" style="margin-top: 1rem" block @click="EditButton">{{ $t('编 辑') }}</van-button>

        <van-button type="warning" style="margin-top: 1rem" block @click="DeleteButton">{{ $t('删 除') }}</van-button>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  AddIRApplyMessage, // 提交申请信息
  uploadFiles, // 上传文件，获取文件的组id
  updFileStatus, // 启用文件组中的所有文件
  uploadFilesWithAdd, // 更新文件组中的文件
  delAllfiles // 删除文件组中所有的文件
} from '@/model/api'
export default {
  name: 'JobApplication',
  data () {
    return {
      show: false, // 编辑弹出框
      FileName: '上传文件', // 上传的文件名字
      FileId: '', // 文件id
      Personal: { // 个人基础信息
        title: '个人基本信息',
        dataStructure: { // 数据模板
          empNo: '', // 工号
          name: '', // 姓名
          cudeptName: '', // 当前部门
          positionName: '', // 当前岗位
          idnumber: '', // 身份证号
          classPosition: '', // 资位
          managePosition: '', // 管理职
          mobile: '' // 联系方式
        },
        template: [ // 渲染模板
          {title: '工号', value: '', type: 'Text'},
          {title: '姓名', value: '', type: 'Text'},
          {title: '当前部门', value: '', type: 'Text'},
          {title: '当前岗位', value: '', type: 'Text'},
          {title: '身份证号', value: '', type: 'Text'},
          {title: '资位', value: '', type: 'Text'},
          {title: '管理职', value: '', type: 'Text'},
          {title: '联系方式', value: '', type: 'Text'}
        ]
      },
      // 多项
      MultipleEdits: { // 多项栏位
        Multiple: { // 教育经历
          title: '教育经历',
          dataStructure: { // 数据模板
            school: '', // 学校
            qualification: '', // 学历
            major: '', // 专业
            beginTime: '', // 开始时间
            endTime: '' // 结束时间
          },
          template: [ // 渲染模板
            {title: '学校', value: '', type: 'Text'},
            {title: '学历', value: '', type: 'Picker', columns: ['博士', '硕士', '研究生', '本科', '大专', '高中/中专及以下']},
            {title: '专业', value: '', type: 'Text'},
            {title: '开始时间', value: '', type: 'Time'},
            {title: '结束时间', value: '', type: 'Time'}
          ]
        },
        WorkExperience: { // 工作经验
          title: '工作经验',
          dataStructure: { // 数据模板
            company: '', // 公司名称
            position: '', // 职位
            beginTime: '', // 开始时间
            endTime: '', // 结束时间
            workDesc: '' // 工作描述
          },
          template: [ // 渲染模板
            {title: '公司名称', value: '', type: 'Text'},
            {title: '职位', value: '', type: 'Text'},
            {title: '开始时间', value: '', type: 'Time'},
            {title: '结束时间', value: '', type: 'Time'},
            {title: '工作描述', value: '', type: 'textarea'}
          ]
        },
        ProjectExperience: { // 项目经验
          title: '项目经验',
          dataStructure: { // 数据模板
            company: '', // 项目名称
            beginTime: '', // 开始时间
            endTime: '', // 结束时间
            expEdesc: '' // 项目简介
          },
          template: [ // 渲染模板
            {title: '项目名称', value: '', type: 'Text'},
            {title: '开始时间', value: '', type: 'Time'},
            {title: '结束时间', value: '', type: 'Time'},
            {title: '项目简介', value: '', type: 'textarea'}
          ]
        }
      },
      // 调动理由
      // ReasonForTransfer: '',
      // 弹窗锁定要编辑的数据
      DataAlert: {
        title: '',
        key: '',
        index: null,
        data: ''
      }
    }
  },
  computed: {
    PersonalSql () {
      return this.$store.getters.getPersonal
    },
    ReasonForTransfer: {
      get () {
        return this.$store.getters.getReasonForTransfer
      },
      set (value) {
        let obj = {
          ReasonForTransfer: value
        }
        this.$store.commit('update', obj)
      }
    }
  },
  mounted () {
    // console.log(this.$store.getters)
    // console.log(this.$route.query.Id)
  },
  methods: {
    // 多项弹窗
    showPopup (title, key, data, index) {
      this.show = true
      this.DataAlert.title = title
      this.DataAlert.key = key
      this.DataAlert.data = data
      this.DataAlert.index = index
    },
    // 统一跳转编辑页面
    routerPushClick (key, data, type) {
      this.$router.push({
        name: 'BasicInformation',
        params: {
          key: key,
          // 指针1：0增1删2改 指针2：-1单项 其他为指针
          type: type,
          dataStructure: data.dataStructure,
          title: data.title,
          template: data.template
        }
      })
    },
    // 多项编辑
    EditButton () {
      let DataAlert = this.DataAlert
      this.$router.push({
        name: 'BasicInformation',
        params: {
          key: DataAlert.key,
          // 指针1：0增1删2改 指针2：-1单项 其他为指针
          type: [ 2, DataAlert.index ],
          dataStructure: DataAlert.data.dataStructure,
          title: DataAlert.data.title,
          template: DataAlert.data.template
        }
      })
    },
    // 多项删除
    DeleteButton () {
      let DataAlert = this.DataAlert
      this.$store.commit('MultipleEdits', {type: [1, DataAlert.index], key: DataAlert.key})
      this.$toast(this.$t('删除成功'))
      this.show = false
    },
    // 文件上传功能
    FileUpload (file) {
      this.$toast(this.$t('文件上传'))
      console.log(file)
      console.log(this.FileId)
      this.FileName = file.file.name
      // 文件上传
      var FileFormData = new FormData() // 处理需要上传文件
      FileFormData.append('files', file.file) // 文件
      FileFormData.append('emp', this.$store.getters.getInfo.empno) // 工号
      FileFormData.append('fileDesc', this.$store.getters.getInfo.empno + '推荐') // 文件描述
      if (!this.FileId) { // 判断当前需求单是否绑定了组id
        uploadFiles(FileFormData).then(res => { // 上传文件，获取文件组id
          console.log('文件上传', res)
          console.log(res)
          if (res.code === 0) {
            this.FileId = res.data
            this.$toast(this.$t('上传成功'))
          } else {
            this.$toast('uploadFiles' + res.msg)
          }
        })
        console.log('文件组id' + this.FileId)
      } else {
        FileFormData.append('groupId', this.FileId) // 文件组(必传)
        // 删除文件组中的所有文件
        delAllfiles({
          groupId: this.FileId
        }).then(res => {
          if (res.code === 0) {
            // 上传文件
            uploadFilesWithAdd(FileFormData).then(res => {
              if (res.code === 0) {
                this.$toast(this.$t('上传成功'))
              }
            })
          }
        })
      }
      // window.cm.openFile({
      //   type:type,  // 文件类型
      //   url:url,  // 文件路径
      //   deal: function (url) {
      //     // 在非香信环境下处理方法
      //     // 参数url：当前文件的url
      //   }
      // })
    },
    // 删除文件
    DeleteFile () {
      this.FileName = '上传文件'
      this.FileId = ''
      this.$toast(this.$t('删除成功'))
    },
    // 提交申请
    SubmitApplication () {
      // console.log(this.$store.getters.getPersonal)
      // console.log(this.$store.getters.getMultiple.SqlData)
      // console.log(this.$store.getters.getWorkExperience.SqlData)
      // console.log(this.$store.getters.getProjectExperience.SqlData)
      // console.log(this.ReasonForTransfer)
      let obj = {
        applyBasic: this.$store.getters.getPersonal.data, // 申請人基本信息
        applyEducations: this.$store.getters.getMultiple.SqlData, // 申請人學習經歷
        applyWorkinglives: this.$store.getters.getWorkExperience.SqlData, // 申請人工作經歷
        applyExperiences: this.$store.getters.getProjectExperience.SqlData // 申請人項目經歷
      }
      obj.applyBasic.demandNo = this.$route.query.Id // 绑需求单号
      obj.applyBasic.fileId = this.FileId
      obj.applyBasic.applyReason = this.ReasonForTransfer // 内调原因
      if (obj.applyBasic.name === '') {
        this.$toast(this.$t('请填写个人基本信息'))
        return
      }
      if (obj.applyEducations.length === 0) {
        this.$toast(this.$t('请填写教育经历'))
        return
      }
      if (obj.applyWorkinglives.length === 0) {
        this.$toast(this.$t('请填写工作经验'))
        return
      }
      // if (obj.applyExperiences.length === 0) {
      //   this.$toast('请填写项目经验')
      //   return
      // }
      if (obj.applyBasic.applyReason === '') {
        this.$toast(this.$t('请填写内调原因'))
        return
      }
      if (this.FileId) {
        // 启用文件
        updFileStatus({}, { // 启用文件组中的所有文件
          fileGroup: this.FileId
        }).then(FileRes => {
          if (FileRes.code === 0) {
            // 保存数据
            AddIRApplyMessage(obj).then(res => {
              if (res.status === 0) {
                this.$router.push({name: 'TipText', params: { Text: '我的申请' }})
              } else {
                this.$toast('AddIRApplyMessage' + res.message)
              }
            })
          } else {
            this.$toast('AddIRApplyMessage' + FileRes.msg)
          }
        })
      } else {
        // 保存数据
        AddIRApplyMessage(obj).then(res => {
          if (res.status === 0) {
            this.$router.push({name: 'TipText', params: { Text: '我的申请' }})
          } else {
            this.$toast('AddIRApplyMessage' + res.message)
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #JobApplication{
    height: 100%;
    background-color: #f4f7fb;
    ul,li{
      list-style: none;
      margin: 0;
      padding: 0;
    }
    .TopFixed{
      width: 100%;
      background-color: cornflowerblue;
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
    }
    .FormBody{
      height: calc( 100% - 3rem );
      padding-top: 2.8rem;
      text-align: left;
      overflow-x: hidden;
      overflow-y: auto;
      /*个人信息 - 开始*/
      .Basic_Information{
        background-color: white;
        padding: 1rem;
        div{
          margin:2px 0 ;
          color: #696969;
          font-size: 0.7rem;
          span{
            color:#000;
          }
        }
        .name{
          font-size: 1.5rem;
        }
        .BasicButton{
          float: right;
          font-size: 0.9rem;
          line-height:2rem;
          color: cornflowerblue;
          span{
            color: cornflowerblue;
            vertical-align: 2px;
          }
        }
      }
      /*个人信息 - 结束*/

      /*多项-公共样式 - 开始*/
      .Multiple{
        margin-top: 0.3rem;
        padding-left: 0.6rem;
        box-sizing: border-box;
        background-color: white;
        .Title{
          padding:0.3rem;
          font-size: 1.1rem;
          line-height: 2rem;
          border-bottom:#f4f7fb solid 1px ;
          .BasicButton{
            float: right;
            padding-right: 0.8rem;
            font-size: 0.9rem;
            line-height:2rem;
            color: cornflowerblue;
            span{
              color: cornflowerblue;
              vertical-align: 2px;
            }
          }
        }
      }
      /*多项-公共样式 - 结束*/

      /*单项 --开始*/
      .SingleItem{
        margin-top: 0.3rem;
        padding-left: 0.6rem;
        box-sizing: border-box;
        background-color: white;
        .Title{
          padding:0.3rem;
          font-size: 1.1rem;
          line-height: 2rem;
          border-bottom:#f4f7fb solid 1px ;
          .BasicButton{
            float: right;
            font-size: 0.9rem;
            line-height:2rem;
            color: cornflowerblue;
            span{
              color: cornflowerblue;
              vertical-align: 2px;
            }
          }
        }
      }
      /*单项 --结束*/

      // 内调原因 -开始
      .ReasonTransfer{
        margin-top: 0.3rem;
        background-color: white;
        padding-left: 0.6rem;
        box-sizing: border-box;
        .Title {
          padding: 0.3rem;
          font-size: 1.1rem;
          line-height: 2rem;
          border-bottom:#f4f7fb solid 1px ;
        }
      }
      // 内调原因 -结束

      // 文件
      .FileBtnCss {
        display: flex;
        align-items: center;
      }
    }
  }
</style>
<style lang="scss">
  #JobApplication{
    .van-cell__value,.van-cell__right-icon{
      color: cornflowerblue;
    }
    .FileBtnCss {
      padding:0.2rem 0.7rem;
      .van-button--normal {
        padding: 6px 8px;
        font-size: 12px;
      }
      .van-button {
        height: 100%;
      }
    }
  }
</style>



