export default {
  MultipleEdits (state, v) { // 多项编辑
    // 0为新增 1为删除 2修改
    if (v.type[0] === 0) {
      state[v.key].ListData.push(v.ListData)
      state[v.key].SqlData.push(v.SqlData)
    } else if (v.type[0] === 1) {
      state[v.key].ListData.splice(v.type[1], 1)
      state[v.key].SqlData.splice(v.type[1], 1)
    } else if (v.type[0] === 2) {
      state[v.key].ListData[v.type[1]] = v.ListData
      state[v.key].SqlData[v.type[1]] = v.SqlData
    }
  },
  SingleEdit (state, v) { // 单项
    state[v.key].start = true
    state[v.key].data = v.SqlData
  },
  // 数据更新
  update (state, v) {
    for (let key in v) {
      state[key] = v[key]
    }
  }
}



