// 拦截文件
import axios from 'axios'
import store from '../store/index.js'

// axios.defaults.baseURL = window.location.origin + '/forward/rms/api/' // api地址

let baseURL = ''
if (window.location.hostname === '10.167.214.11' || window.location.hostname === 'localhost') {
  baseURL = window.location.origin + '/forward/rms/api/'
} else {
  // baseURL = 'https://microapps.foxconn.com/ocelotservice/rms/forward/rms/api/'
  baseURL = 'https://appvn5.foxconn.com:8008/RMSOcelotService/rms/'
}
// 创建一个实例
const request = axios.create({
  baseURL: baseURL, // api地址
  // 请求延时为3000毫秒
  timeout: 30000
})

// 拦截器 请求拦截
request.interceptors.request.use(config => {
  // 配置项  携带的数据(部分接口需要拿到token)
  config.headers = {
    Authorization: store.getters.getInfo.Token,
    empno: store.getters.getInfo.empno
  }
  return config
}, err => {
  return Promise.reject(err)
})

// 拦截器 响应拦截
request.interceptors.response.use(res => {
  return res
}, err => {
  return Promise.reject(err)
})

// get
export function get (url, params) {
  return new Promise((resolve, reject) => {
    request({
      method: 'get',
      url,
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err)
    })
  })
}
/**
 * post方法，对应post请求
 * @url {String} url [请求的url地址]
 * @data {Object} data [请求时携带的参数]
 * @param {Object} params [请求时url拼接的参数]
 */
export function post (url, data, params) {
  return new Promise((resolve, reject) => {
    request({
      method: 'post',
      url: url,
      headers: {
        'content-type': 'application/json;charset=UTF-8'
      },
      data: data,
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

/**
 * put方法，对应put请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function put (url, data, params) {
  return new Promise((resolve, reject) => {
    request({
      method: 'put',
      url: url,
      headers: {
        'content-type': 'application/json;charset=UTF-8'
      },
      data: data,
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

/**
 * Delete方法，对应Delete请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function Delete (url, params) {
  return new Promise((resolve, reject) => {
    request({
      method: 'delete',
      url: url,
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}



