<template>
  <div id="JobDetails">
    <van-nav-bar
      :title="$t('职位详情')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />
    <div class="Content">
      <!--标题-->
      <div class="DetailsTitle">
        <div class="Title"><b>{{$t(JobDetailsData.Title)}}</b></div>
        <div class="Column"> <span>{{$t(JobDetailsData.Column)}}</span> <span>{{JobDetailsData.Time}}</span></div>
      </div>
      <!--职位详情-->
      <div class="JobIntroduction">
        <h3>{{ $t('职位详情') }}</h3>
        <ul>
          <li>
            <div class="flexCantter">
              <div><b>{{ $t('工作年限：') }}</b>{{ JobDetailsData.workinglife }}</div>
              <div><b>{{ $t('招聘人数：') }}</b>{{ JobDetailsData.requireQty }}</div>
              <div><b>{{ $t('学历要求：') }}</b>{{ JobDetailsData.qualification }}</div>
              <div><b>{{ $t('工作地点：') }}</b>{{ JobDetailsData.workLocation }}</div>
            </div>
          </li>
        </ul>
        <!--工作职责-->
        <ul class="ClaimList">
          <li><b>{{ $t('工作职责：') }}</b></li>
          <li v-for="(item, index) in JobDetailsData.Claim" :key="index">{{item}}</li>
        </ul>
        <!--岗位要求-->
        <ul class="ClaimList">
          <li><b>{{ $t('岗位要求：') }}</b></li>
          <li v-for="(item, index) in JobDetailsData.Duty" :key="index">{{item}}</li>
        </ul>
      </div>
    </div>

    <!--分享-->
    <van-share-sheet
      v-model="ShareShow"
      :title="$t('分享')"
      :options="ShareArr"
      @select="onSelect"
      @close="onClose()"
    />

    <van-goods-action style="border-top: #F1F1F1 solid 1px">
      <van-goods-action-icon
        :icon=" JobDetailsData.IsCollection === 1 ? 'star' : 'star-o' "
        :text=" JobDetailsData.IsCollection === 1 ? $t('取消收藏') : $t('收藏') "
        :color=" JobDetailsData.IsCollection === 1 ? '#07c160' : '#323233' "
        @click=" onClickIcon(0) " />
      <!-- <van-goods-action-icon icon="share-o" text="分享" @click=" onClickIcon(1) " /> -->
      <!-- <van-goods-action-icon icon="award-o" text="推荐" @click=" onClickIcon(2) " /> -->
      <van-goods-action-button
        type="primary" :text="$t('推荐')" style="border-right: #e3f2fd solid 1px;"
        @click=" onClickIcon(2) "
      />
      <van-goods-action-button
        :type="JobDetailsData.isApply === 1 ? 'warning' : 'primary'"
        :text="JobDetailsData.isApply === 1 ? $t('已申请') : $t('立即申请')"
        @click="onClickButton"
      />
    </van-goods-action>
  </div>

</template>

<script>
import {
  GetIRMessage, // 獲得需求崗位的詳細信息
  SetMyFavourite // 收藏，取消收藏
} from '@/model/api'
export default {
  name: 'JobDetails',
  data () {
    return {
      JobDetailsData: {
        Title: '',
        Column: '',
        Time: '',
        workinglife: '', // 工作年限
        IsCollection: 0, // 是否被我收藏。0没有，1有
        isApply: 0, // 是否已申请过当前岗位
        Duty: [],
        Claim: []
      },
      ShareShow: false, // 分享
      ShareArr: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' }
        ]
      ]
    }
  },
  mounted () {
    // console.log(this.$route.query.Id)
    GetIRMessage({
      demandNo: this.$route.query.Id,
      type: 'phone'
    }).then(res => {
      if (res.status === 0) {
        this.JobDetailsData.Title = res.data.positionName
        this.JobDetailsData.Column = res.data.positionI
        this.JobDetailsData.Time = res.data.publishDate
        this.JobDetailsData.workinglife = res.data.workinglife
        this.JobDetailsData.requireQty = res.data.requireQty
        this.JobDetailsData.qualification = res.data.qualification
        this.JobDetailsData.workLocation = res.data.workLocation
        this.JobDetailsData.IsCollection = res.data.isCollection // 是否被我收藏。0没有，1有
        this.JobDetailsData.isApply = res.data.isApply // 是否已申请
        this.JobDetailsData.Duty = res.data.conditions ? res.data.conditions.split('\n') : ''
        this.JobDetailsData.Claim = res.data.workMajor ? res.data.workMajor.split('\n') : ''
      }
    })
  },
  methods: {
    onClickIcon (num) {
      if (num === 0) { // 收藏岗位
        SetMyFavourite({}, {
          demandNo: this.$route.query.Id
        }).then(res => {
          if (res.status === 0) {
            this.JobDetailsData.IsCollection = res.data
            this.$toast('SetMyFavourite' + res.message)
          }
        })
      } else if (num === 1) { // 分享岗位
        this.ShareShow = true
      } else if (num === 2) { // 岗位推荐
        this.$router.push({path: '/JobRecommend', query: { Id: this.$route.query.Id }})
      }
    },

    onClose () {
      this.ShareShow = false
    },
    // 点击分享选项时触发
    onSelect (option, index) {
      console.log(option, index)

      let useCivet = window.cm.useCivet
      console.log(window.cm.useCivet)
      console.log(window.cm)
      if (useCivet === true) {
        // this.$toast('是香信环境。')
        // 跳转到用户基本信息页面
        // window.cm.chat({
        //   civetno: 'X2000847', // 个人香信号,为必填项
        //   fail: function (errmsg) {
        //     // 调用失败时执行的回调函数
        //     // 参数errmsg：失败原因，字符串型
        //     this.$toast(errmsg)
        //   }
        // })
      } else {
        this.$toast(this.$t('不是香信环境。'))
      }

      if (option) {
        // this.$toast(option.name)
      }
      this.onClose()
    },

    onClickButton () { // 申请岗位
      if (this.JobDetailsData.isApply === 1) {
        this.$toast(this.$t('您已申请过当前岗位'))
        return
      }
      this.$router.push({path: '/JobApplication', query: { Id: this.$route.query.Id }})
    }
  }
}
</script>

<style scoped lang="scss">
  #JobDetails{
    background-color: white;
    text-align: left;
    height: 100%;
    .Content{
      padding: 0 1rem;
      height: calc(100% - 97px);
      overflow-x: hidden;
      overflow-y: auto;
      /*头部标题*/
      .DetailsTitle{
        line-height: 25px;
        border-bottom: #CCCCCC solid 1px;
        padding: 10px 0;
        .Title{
          font-size: 20px;
        }
        .Column{
          font-size: 12px;
          span{
            margin-right: 10px;
          }
        }
      }
      /*职位详情*/
      .JobIntroduction{
        ul,li{
          list-style: none;
          padding: 0;
          margin: 0;
          font-size: 12px;
          line-height: 20px;
        }
        .ClaimList{
          margin-top: 10px;
        }
        .flexCantter{
          display: flex;
          flex-wrap: wrap;
          div {
            width: 50%;
          }
        }
      }
    }
  }
</style>
<style lang="scss">
  #JobDetails {
    .van-goods-action-icon {
      min-width: 80px;
    }
  }
</style>



