<template>
  <div id="BasicInformation">
    <!-- 职位详情 > 职位申请 > 教育经历，工作经验，项目经验用的底层填写页面-->
    <div class="TopFixed">
      <van-nav-bar
        :title="BasicInformation.title"
        :left-text="$t('返回')"
        left-arrow
        @click-left=" $router.go(-1)"
      />
    </div>
    <div class="FormBody">
      <!--Text：输入框；Picker：选择器 Time：时间选择器 textarea：文本域-->
      <div v-for="(item, index) in BasicInformation.template" :key="index">
        <van-cell-group  v-if="item.type === 'Picker' || item.type === 'Time' ">
          <van-cell @click="Component = item; show = true " :title="$t(item.title)" :value="item.value ==''?$t('请选择'):item.value"/>
        </van-cell-group>
        <van-cell-group  v-else-if="item.type === 'textarea'">
          <van-field
            v-model="item.value"
            rows="2"
            autosize
            :label="$t(item.title)"
            type="textarea"
            :placeholder="$t('请输入')+item.title"
          />
        </van-cell-group>
        <van-cell-group v-else>
          <van-field  v-model="item.value" :label="$t(item.title)" :placeholder="$t('请输入')"  input-align="right" @change="FieldChange(item.title, item.value)"/>
        </van-cell-group>

      </div>

      <!--按钮-->
      <div style="padding:1rem">
        <van-button type="primary" style="margin-top: 1rem" block @click="routeReturn">{{ $t('保 存') }}</van-button>
      </div>
    </div>

    <van-popup v-model="show" position="bottom" :style="{ height: 'auot' }" >
      <van-picker
        v-if="Component.type === 'Picker'"
        :title="$t(Component.title)"
        show-toolbar
        :columns="Component.columns"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
      <van-datetime-picker
        v-else
        type="date"
        :title="$t(Component.title)"
        v-model="currentDate"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>

  </div>
</template>

<script>
import {
  GetIRMyMessage // 获取我填单的历史信息
} from '@/model/api'
export default {
  name: 'BasicInformation',
  data () {
    return {
      minDate: new Date(2000, 0), // 时间选择范围，小
      maxDate: new Date(2099, 12), // 时间选择范围，大
      currentDate: new Date(), // 打开后的默认时间显示时间
      ListData: {
        ListTile: '',
        label: ''
      },
      show: false,
      Component: {} // 选择器指向数据
    }
  },
  computed: {
    BasicInformation () {
      // let OBJ = JSON.parse(JSON.stringify(this.$route.params));
      let OBJ = this.$route.params
      if (OBJ.type[0] === 2) {
        let i = 0
        for (let MyKey in OBJ.dataStructure) {
          i++
          if (OBJ.type[1] !== -1) {
            OBJ.template[i - 1].value = (this.$store.getters['get' + OBJ.key].SqlData[OBJ.type[1]])[MyKey]
          } else {
            OBJ.template[i - 1].value = this.$store.getters['get' + OBJ.key].data[MyKey]
          }
        }
      }
      return OBJ
    }
  },
  mounted () {
    console.log(this.$store.getters.getPersonal.data.empNo)
    if (this.BasicInformation.title === '个人基本信息' && this.$store.getters.getPersonal.data.empNo === '') {
      this.BasicInformation.template[0].value = this.$store.getters.getInfo.empno
      this.FieldChange('工号', this.BasicInformation.template[0].value)
    }
  },
  methods: {
    // change事件
    FieldChange (key, value) {
      if (key === '工号' && this.BasicInformation.title === '个人基本信息') {
        GetIRMyMessage({
          type: 'Apply',
          empNo: value
        }).then(res => {
          if (res.status === 0 && res.data) {
            this.BasicInformation.template[1].value = res.data.NAME
            this.BasicInformation.template[2].value = res.data.DEPTNAME
            this.BasicInformation.template[3].value = res.data.POSITIONNAME
            this.BasicInformation.template[4].value = res.data.IDNUMBER
            this.BasicInformation.template[5].value = res.data.CLASSPOSITION
            this.BasicInformation.template[6].value = res.data.MANAGEPOSITION
            this.BasicInformation.template[7].value = res.data.MOBILE
          }
        })
      }
    },
    // 保存信息
    routeReturn () {
      for (let i = 0; i < this.BasicInformation.template.length; i++) {
        if (this.BasicInformation.template[i].value === '') {
          // Toast(this.BasicInformation.template[i].title + '不能为空')
          this.$toast(this.BasicInformation.template[i].title + '不能为空')
          return
        }
      }
      let OBJ = {
        ListData: null,
        SqlData: null,
        type: null
      }
      let i = 0
      for (let key in this.BasicInformation.dataStructure) {
        i++
        this.BasicInformation.dataStructure[key] = this.BasicInformation.template[i - 1].value
        if (i - 1 === 0) this.ListData.ListTile = this.BasicInformation.template[i - 1].value
        if (i - 1 === 1) this.ListData.label = this.BasicInformation.template[i - 1].value
      }
      OBJ.ListData = this.ListData
      OBJ.SqlData = this.BasicInformation.dataStructure
      OBJ.type = this.BasicInformation.type
      OBJ.key = this.BasicInformation.key
      // this.$store.commit('set'+this.BasicInformation.key,OBJ);
      if (this.BasicInformation.type[1] === -1) {
        this.$store.commit('SingleEdit', OBJ)
      } else {
        this.$store.commit('MultipleEdits', OBJ)
      }
      this.$router.go(-1)
    },
    // 日期转换
    getNowFormatDate (date) {
      var seperator1 = '-'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate
      return currentdate
    },
    // 组件
    onConfirm (value, index) {
      if (this.Component.type === 'Time') {
        this.Component.value = this.getNowFormatDate(value)
        this.show = false
      } else {
        this.Component.value = value
        this.show = false
      }
    },
    // onChange(picker, value, index) {
    // this.$toast(`当前值：${value}, 当前索引：${index}`);
    // },
    onCancel () {
      this.show = false
    }
  }
}
</script>

<style lang="scss" scoped>
 #BasicInformation{
   height: 100%;
   .TopFixed{
     width: 100%;
     background-color: cornflowerblue;
     position: fixed;
     z-index: 10;
     top: 0;
     left: 0;
   }
   .FormBody{
     height: calc( 100% - 3rem );
     padding-top: 2.8rem;
     text-align: left;
     overflow-x: hidden;
     overflow-y: auto;
   }
 }
</style>



