<template>
  <div id="DatetimePickerModule">
    <van-popup v-model="ModelContent.show" position="bottom" :style="{ height: '50%' }">
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        :title="$t(ModelContent.title)"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
        @cancel="confirm"
        @confirm="confirm"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'DatetimePickerModule',
  data () {
    return {
      minDate: new Date(2000, 0), // 时间选择范围，小
      maxDate: new Date(2099, 12), // 时间选择范围，大
      currentDate: new Date(), // 打开后的默认时间显示时间
      outputYear: '', // 输出年
      outputMonth: '' // 输出月
    }
  },
  props: {
    ModelContent: {
      type: Object,
      default: () => {
        return {
          title: '选择时间',
          show: false
        }
      }
    }
  },
  watch: {
    'ModelContent.show': function (val) {
      if (!val) {
        let date = this.outputYear + '年' + this.outputMonth + '月'
        this.$emit('GetDate', date)
      }
    }
  },
  mounted () {
    console.log(this.ModelContent)
  },
  methods: {
    // 时间选择器格式处理
    formatter (type, val) {
      if (type === 'year') {
        this.outputYear = val
        return `${val}年`
      } else if (type === 'month') {
        this.outputMonth = val
        return `${val}月`
      }
      return val
    },
    confirm (value) {
      this.ModelContent.show = false
    }
  }
}
</script>

<style lang="scss" scoped>
#DatetimePickerModule {

}
</style>



