<template>
  <div id="ApplicationCheck">
    <van-nav-bar
      :title="$t('申请详情')"
      :left-text="$t('返回')"
      left-arrow
      @click-left=" $router.go(-1)"
    />
    <div class="Content">
      <div class="Title">{{$t('申请人基本信息')}}</div>
      <table cellpadding="0" cellspacing="0">
        <tr>
          <td rowspan="3" width="60px">
            <div>
              <van-icon name="user-circle-o" color="#00aaff" size="3rem" />
            </div>
          </td>
          <td>
            <div class="FlexLeft">
              <span>{{ applyBasic.name }}</span>
              ({{ applyBasic.empNo }})
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ applyBasic.deptName }}
            </div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="FlexLeft">
              {{ applyBasic.classPosition }}
              ({{ applyBasic.managePosition }})
              ({{ applyBasic.positionName }})
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('身份证号：')}}{{ applyBasic.idnumber }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('申请编号：')}}{{ applyBasic.applyId }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="FlexLeft">
              {{$t('申请原因：')}}{{ applyBasic.applyReason }}
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2" style="border-bottom: none;">
            <div v-for="item in FileArr" :key="item.url">
              <span @click='FileClick(item.url)' style="color: #6337b6;cursor: pointer;">{{ item.name }}</span>
            </div>
          </td>
        </tr>
      </table>
      <div class="Title">{{$t('教育经历')}}</div>
      <div>
        <van-collapse v-model="activeEducations">
          <van-collapse-item v-for="(item, index) in applyEducations" :key="index" :name="index" style="margin: 0 0 0.5rem 0;">
            <template #title>
              <div>{{ item.qualification }} | {{ item.major }}</div>
            </template>
            <van-tag type="success">{{ item.beginTime }} 至 {{ item.endTime }}</van-tag>
            <van-tag type="warning">{{ item.school }}</van-tag>
          </van-collapse-item>
        </van-collapse>
      </div>
      <div class="Title">{{$t('工作经历')}}</div>
      <div>
        <van-collapse v-model="activeWorkinglives">
          <van-collapse-item v-for="(item, index) in applyWorkinglives" :key="index" :name="index" style="margin: 0 0 0.5rem 0;">
            <template #title>
              <div>{{ item.company }} | {{ item.position }}</div>
            </template>
            <van-tag type="success">{{ item.beginTime }} {{$t('至')}} {{ item.endTime }}</van-tag>
            <van-tag type="warning">{{ item.workDesc }}</van-tag>
          </van-collapse-item>
        </van-collapse>
      </div>
      <div class="Title">{{$t('项目经历')}}</div>
      <div>
        <van-collapse v-model="activeExperiences">
          <van-collapse-item v-for="(item, index) in applyExperiences" :key="index" :name="index" style="margin: 0 0 0.5rem 0;">
            <template #title>
              <div>{{ item.company }}</div>
            </template>
            <van-tag type="success">{{ item.beginTime }} {{$t('至')}} {{ item.endTime }}</van-tag>
            <van-tag type="warning">{{ item.expEdesc }}</van-tag>
          </van-collapse-item>
        </van-collapse>
      </div>
      <div class="bottomBtnCss">
        <van-button type="info" @click="$router.go(-1)" round style="width: 100%;">{{$t('返回')}}</van-button>
      </div>
    </div>
  </div>

</template>

<script>
import {
  GetIRApplyMessage, // 獲得需求崗位的詳細信息
  getFileInfo // 获取文件
} from '@/model/api'
export default {
  name: 'ApplicationCheck',
  data () {
    return {
      applyBasic: {}, // 基本信息
      applyEducations: [], // 学历信息
      activeEducations: [],
      applyWorkinglives: [], // 工作经历
      activeWorkinglives: [],
      applyExperiences: [], // 项目经历
      activeExperiences: [],
      FileArr: [] // 文件
    }
  },
  mounted () {
    // console.log(this.$route.query.Id)
    GetIRApplyMessage({
      applyId: this.$route.query.Id
    }).then(res => {
      if (res.status === 0) {
        this.applyBasic = res.data.applyBasic
        this.applyEducations = res.data.applyEducations
        this.applyWorkinglives = res.data.applyWorkinglives
        this.applyExperiences = res.data.applyExperiences
        if (this.applyBasic.fileId) {
          this.getFile()
        }
      }
    })
  },
  methods: {
    getFile () {
      getFileInfo({
        groupId: this.applyBasic.fileId
      }).then(res => {
        if (res.code === 0) {
          this.FileArr = []
          res.data.fileList.map(item => {
            let suffix = item.filePath.split('.')
            this.FileArr.push({
              id: item.fileId,
              name: item.fileName + '.' + suffix[suffix.length - 1],
              url: item.filePath
            })
          })
        } else {
          this.$toast('getFileInfo' + res.msg)
        }
      })
    },
    // 文件打开
    FileClick (url) {
      // url = url.replace('http://10.167.214.11:8003/', 'https://microapps.foxconn.com/ocelotservice/rms/forward/rmsfile')
      url = url.replace('http://10.167.214.11:8003/', 'https://appvn5.foxconn.com:8008/RMSOcelotService/rmsfile/rmsfile')
      window.open(url)
    }
  }
}
</script>

<style scoped lang="scss">
  #ApplicationCheck{
    background-color: white;
    text-align: left;
    height: 100%;
    .Content{
      padding: 0 1rem;
      height: calc(100% - 97px);
      overflow-x: hidden;
      overflow-y: auto;
      .Title {
        font-size: 15px;
        font-weight: bolder;
        margin: 0.5rem 0 0.2rem 0;
      }
      table {
        width: 100%;
        border: 0.2px solid #a6a6a6;
        border-right: none;
        tr {
          height: 25px;
          td {
            min-width: 40px;
            line-height: 25px;
            font-size: 13px;
            text-align: center;
            border-right: 0.2px solid #a6a6a6;
            border-bottom: 0.2px solid #a6a6a6;
            border-collapse: collapse;
            box-sizing: border-box;
            .FlexLeft {
              display: flex;
              justify-content: flex-start;
              align-items: center;
              padding: 0 0 0 1rem;
              span {
                font-size: 1rem;
                padding: 0 0.2rem 0 0 ;
                font-weight: 600;
              }
            }
          }
        }
      }
      .bottomBtnCss {
        margin-top: 2rem;
        display: flex;
        justify-content: space-around;
      }
    }
  }
</style>
<style lang="scss">
  #ApplicationCheck {
    .van-goods-action-icon {
      min-width: 80px;
    }
    .van-cell {
      padding: 0px 5px;
      background: #e8f6f0;
    }
    .van-collapse-item__content {
      background-color: #9e9e9e30;
    }
  }
</style>



